import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import ReactDOM from 'react-dom';
import { APP_CITY_LATITUDE, APP_CITY_LONGITUDE } from '../../core/utils/IConstant';
import { asyncGetListAction, setSelectedTabIndexAction, goBackAction } from '../../store/actions/culture-map/listAction';

class CultureMapScreen extends Component {
    tags = [
        [{
            title: '人物',
            index: 0,
        }, {
            title: '建筑',
            index: 1,
        }, {
            title: '故事',
            index: 2,
        }],
        [{
            title: '民俗',
            index: 3,
        }, {
            title: '声音',
            index: 4,
        }, {
            title: '美食',
            index: 5,
        }],
    ];
    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state = {
            mapHeight: 0,
            checkedIndex: 0,
            isTagRowsShow: false,
            checkedName:'人物',
        };
    }
    componentDidMount() {
        this.updateWindowDimensions();

        this.loadData();
        
    }

    loadData(){
        this.props.dispatch(asyncGetListAction(this.state.checkedName));
    }
    componentWillReceiveProps(nextProps) {
        if (!this.props.baiduMapInited && nextProps.baiduMapInited) {
            this.initMap();
        }
        // if(this.props.cultureMap.selectedTabIndex !== nextProps.cultureMap.selectedTabIndex){
        //     this.initMark();        
        // }
    }

    componentDidUpdate(){
        if (this.map) {
            this.updateMark();    
        }            
    }
    render() {
        return (
            <div className="cultureMap">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    rightContent={this.renderRightMenu()}
                    onLeftClick={() => {
                        this.props.history.goBack();
                        this.props.dispatch(goBackAction());
                    }}>
                    文化地图
                </NavBar>
                <Content>
                    <div className="cultureType" style={{ display: !this.state.isTagRowsShow ? 'none' : null }}>
                        {
                            this.renderSeletctTag()
                        }
                    </div>
                    <div id="cultureBaiduMap" style={{ height: (this.state.mapHeight + 'px') }}></div>
                </Content>
            </div>
        );
    }

    updateMark(){
        this.map.clearOverlays();
        // 地图标记图片
        let icon = require('../../assets/images/map/culture_map_0.png');
        if(this.state.checkedIndex === 0){
            icon = require('../../assets/images/map/culture_map_0.png');
        }else if(this.state.checkedIndex === 1){
            icon = require('../../assets/images/map/culture_map_1.png');
        }else if(this.state.checkedIndex === 2){
            icon = require('../../assets/images/map/culture_map_2.png');
        }else if(this.state.checkedIndex === 3){
            icon = require('../../assets/images/map/culture_map_3.png');
        }else if(this.state.checkedIndex === 4){
            icon = require('../../assets/images/map/culture_map_4.png');
        }else if(this.state.checkedIndex === 5){
            icon = require('../../assets/images/map/culture_map_5.png');
        }
        let markerIcon = new window.BMap.Icon(icon, new window.BMap.Size(30, 30), {
            anchor: new window.BMap.Size(15, 15),
            imageSize: new window.BMap.Size(30, 30)
        });

        if(this.props.cultureMap && this.props.cultureMap.groups 
            && this.props.cultureMap.groups[this.state.checkedName]
            && this.props.cultureMap.groups[this.state.checkedName].dataList
            && this.props.cultureMap.groups[this.state.checkedName].dataList.length > 0)
        {
            let poiLists = this.props.cultureMap.groups[this.state.checkedName].dataList;
            this.markers = [];
            let pts = [];
            for (let i = 0; i < poiLists.length; i++) {
                let item = poiLists[i];
                let pt  = new window.BMap.Point(item.lng, item.lat);
                let marker = new window.BMap.Marker(pt, {icon: markerIcon});
                this.map.addOverlay(marker); 
                // let marker = new window.BMap.Marker(pt, { icon: this.markerIcon });
                // marker.addEventListener('click', function (e) {
                // e.domEvent.stopPropagation();
                // this.markerTapped(i);
                // }.bind(this));
    
                pts.push(pt);                
            }
            this.map.setViewport(pts);          
        }                 
        
    }

    renderRightMenu() {
        return (
            <img className="cultureMapSift" onClick={this.toggle} src={require('../../assets/images/tour/sift@2x.png')} alt='' />
        );
    }

    toggle() {
        this.setState(previousState => {
            return { isTagRowsShow: !previousState.isTagRowsShow };
        });
    }

    renderSeletctTag() {
        return (
            <div className="tagRows">
                {
                    this.tags.map((row, index) => this.renderTagRowView(row, index))
                }
            </div>
        );
    }

    renderTagRowView(row, index) {
        return (
            <div className="tagRow" key={index}>
                {
                    row.map((item, _index) => this.renderTagItemView(item, _index))
                }
            </div>
        );
    }

    renderTagItemView(item, index) {
        return (
            <div className={item.index === this.state.checkedIndex ? 'tagChecked' : 'tag'} key={index} onClick={() => this.checkedTag(item)}>
                {item.title}
            </div>
        );
    }

    checkedTag(item) {
        this.setState({
            checkedIndex: item.index,
            checkedName: item.title,
        },()=>{
            this.loadData();
            this.props.dispatch(setSelectedTabIndexAction(item.index)); 
        });
    }

    updateWindowDimensions() {
        const height = window.innerHeight - ReactDOM.findDOMNode(this).parentNode.offsetTop;
        this.setState({ mapHeight: height }, () => {
            if (this.props.baiduMapInited) {
                this.initMap();
            }
        });
    }

    initMap() {
        this.map = this.map ? this.map : new window.BMap.Map('cultureBaiduMap');
        this.map.centerAndZoom(new window.BMap.Point(APP_CITY_LONGITUDE, APP_CITY_LATITUDE), 16);
        this.map.addControl(new window.BMap.NavigationControl({ type: window.BMAP_NAVIGATION_CONTROL_SMALL, anchor: window.BMAP_ANCHOR_BOTTOM_RIGHT, enableGeolocation: true, offset: new window.BMap.Size(20, 20) }));
        this.map.addControl(new window.BMap.GeolocationControl({ enableAutoLocation: false }));
    }
}

const mapStateToProps = function (state, ownProps) {
    return {
        baiduMapInited: state.system.baiduMapInited,
        cultureMap : state.cultureMap.listScreen,
    };
};
CultureMapScreen = connect(mapStateToProps)(CultureMapScreen);

export default CultureMapScreen;


