import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import { goBackAction } from '../../store/actions/travel-notes/listAction';
import '../../assets/css/travelNotes.css';
import TravelNotesListView from '../../views/travel-notes/TravelNotesListView';
/**
 * 游记
 */
class TravelNotesListScreen extends Component {
    constructor(props) {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.edit = this.edit.bind(this);
    }

    render() {
        let listViewHeight = window.innerHeight - 45;
        return (
            <div className="informationScreen">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    rightContent={this.renderRightMenu()}                    
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    游记
                </NavBar>
                <Content ref={(view) => { this.scrollView = view; }}>
                    <TravelNotesListView
                        ref={(listView)=>{
                            if(listView)
                            {
                                this.listView = listView.getWrappedInstance();
                            }
                        }}
                        onItemClick={this.onItemClick}
                        height={listViewHeight}
                    />
                </Content>
            </div>
        );
    }

    renderRightMenu(){
        return(
            <img className="travelNoteEditIcon" onClick={this.edit} src={require('../../assets/images/information/icon_edit.png')} alt='' />                    
        );
    }
    edit(){
        this.props.history.push('/travelNotes/edit');
    }
    onItemClick(item) {
        if (!item) {
            return;
        }
        let listView: TravelNotesListView = this.listView;
        if (listView) {
            listView.cacheScrollOffset();
        }

        this.props.history.push(`/travelNotes/detail/${item.ID}`);
    }
}

const mapStateToProps = function (state, ownProps) {
    let obj = state.travelNotes.listScreen.groups;
    return {
        ...obj,
    };
};

TravelNotesListScreen = connect(mapStateToProps)(TravelNotesListScreen);

export default TravelNotesListScreen;


