import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import ActivityListView from '../../views/activity/ActivityListView';
import { setSelectedTabIndexAction, goBackAction } from '../../store/actions/activity/listAction';
import '../../assets/css/activity.css';

class ActivityListScreen extends Component
{
    tabs = [
        {title: '官方活动', type: '1'},
        {title: '个人活动', type: '2'},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }

    render()
    {
        let listViewHeight = window.innerHeight - (45 + 44);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    活动
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><ActivityListView key={index} 
                                type={item.type}
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: ActivityListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push(`/activity/detail/${item.ContentID}`);
    }

}
 
const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.activity.listScreen.selectedTabIndex,
    };
};

ActivityListScreen = connect(mapStateToProps)(ActivityListScreen);

export default ActivityListScreen;


