function _getListRequestAction(orderFiled, orderType, pageIndex, searchKey, extraParams) {
    return {
        type: IConstant.Actions.HOTEL.LIST.GET_LIST_REQUEST,
        orderFiled,
        orderType,
        pageIndex,
        keyword: searchKey,
        extraParams,
    };
}

function _getListSuccessAction(orderFiled, orderType, pageIndex, searchKey, extraParams, result) {
    return {
        type: IConstant.Actions.HOTEL.LIST.GET_LIST_SUCCESS,
        orderFiled,
        orderType,
        pageIndex,
        keyword: searchKey,
        extraParams,
        result
    };
}

function _getListFailureAction(orderFiled, orderType, extraParams, error) {
    return {
        type: IConstant.Actions.HOTEL.LIST.GET_LIST_FAILURE,
        orderFiled,
        orderType,
        extraParams,
        message: error
    };
}


