import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import { goBackActivityAction, goBackInformationAction, goBackTravelNoteAction,setSelectedTabIndexAction, goBackAction } from '../../store/actions/me/collectAction';
import '../../assets/css/activity.css';
import { SpecialStoreKey } from '../../core/utils/IConstant';
import InformationListView from '../../views/information/InformationListView';
import TravelNotesListView from '../../views/travel-notes/TravelNotesListView';
import ActivityListView from '../../views/activity/ActivityListView';
import UIUtil from '../../core/utils/UIUtil';

class MyCollectionScreen extends Component {
    tabs = [
        {title: '资讯', component: InformationListView, storeKey: SpecialStoreKey.COLLECT_INFORMATION},
        {title: '游记', component: TravelNotesListView, storeKey: SpecialStoreKey.COLLECT_TRAVEL_NOTE},
        {title: '活动', component: ActivityListView, storeKey: SpecialStoreKey.COLLECT_ACTIVITY},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render()
    {
        let tabHeight = window.innerHeight - 45;
        let listViewHeight = window.innerHeight - (45 + 44);
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    我的收藏
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=>{
                                let CollectListView = item.component;
                                return <CollectListView key={index} 
                                    ref={(listView)=>{
                                        if(listView)
                                        {
                                            this.tabs[index].listView = listView.getWrappedInstance();
                                        }
                                    }}
                                    type = "4"
                                    storeKey={item.storeKey}
                                    onItemClick={this.onItemClick}
                                    height={listViewHeight}/>;
                            })
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }

    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        if(this.props.selectedTabIndex === 0)
        {
            this.props.history.push(`/information/detail/${item.Code}`);
        }
        else if(this.props.selectedTabIndex === 1)
        {
            this.props.history.push(`/travelNotes/detail/${item.Code}`);
        }
        else if(this.props.selectedTabIndex === 2)
        {
            this.props.history.push(`/activity/detail/${item.Code}`);
        }
    }

    goBack()
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());        
        dispatch(goBackActivityAction(this.getExtraParamsActivity()));
        dispatch(goBackInformationAction(this.getExtraParamsInformation()));
        dispatch(goBackTravelNoteAction(this.getExtraParamsTravelNote()));        
    }

    getExtraParamsActivity() {
        return {
            storeKey: SpecialStoreKey.COLLECT_ACTIVITY,
        };
    }
    getExtraParamsInformation() {
        return {
            storeKey: SpecialStoreKey.COLLECT_INFORMATION,
        };
    }
    getExtraParamsTravelNote() {
        return {
            storeKey: SpecialStoreKey.COLLECT_TRAVEL_NOTE,
        };
    }
}    

const mapStateToProps = function (state) {
    return {
        ...state.me.collect,
    };
};

MyCollectionScreen = connect(mapStateToProps)(MyCollectionScreen);

export default MyCollectionScreen;


