import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import StrategyListView from '../../views/strategy/StrategyListView';
import { setSelectedTabIndexAction, goBackAction } from '../../store/actions/strategy/listAction';
import '../../assets/css/strategy.css';

/**
 * 攻略
 */
class StrategyListScreen extends Component
{
    tabs = [
        {title: '食', type: '1'},
        {title: '住', type: '2'},
        {title: '行', type: '3'},
        {title: '游', type: '4'},
        {title: '购', type: '5'},
        {title: '娱', type: '6'},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }
    render()
    {
        let listViewHeight = window.innerHeight - (45 + 44);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    攻略
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><StrategyListView key={index} 
                                type={item.type}
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: StrategyListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push(`/strategy/detail/${item.ID}`);
    }
}

const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.strategy.listScreen.selectedTabIndex,
    };
};

StrategyListScreen = connect(mapStateToProps)(StrategyListScreen);

export default StrategyListScreen;


