import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Carousel } from 'antd-mobile';
import Content from '../../views/common/Content';

class LineDetailScreen extends Component {
    constructor(props) {
        super(props);
        this.goBack = this.goBack.bind(this);
    }
    
    render() {
        let ImgBg = require('../../assets/images/tour/linedetail_bg.jpg');
        let mapPic = 'http://api.map.baidu.com/staticimage/v2?ak=rEbQDhkj1LktHPyrVvxjTLSe1IIYqGsW&center=116.403874,39.914888&width=300&height=200&zoom=15';
        return (
            <div className="lineDetail">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}
                    rightContent={this.renderRightMenu()} >
                </NavBar>
                <Content>
                    <div className="lineDetailBg" style={{ backgroundImage: `url(${ImgBg})` }}>
                        <div className="title1">线路 | 1天</div>
                        <div className="title2">一日之内，爱上贵阳</div>
                        <div className="title3">贵阳市区一日游</div>
                        <div className="title4">贵阳的一天，内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容</div>
                    </div>
                    <div className="mapPic" style={{ backgroundImage: `url(${mapPic})` }}></div>
                    {
                        this.renderDays()
                    }
                </Content>
            </div>
        );
    }

    renderRightMenu() {
        return (
            <div className="lineTitleRight">
                <img className="lineTitleXX" src={require('../../assets/images/tour/collect.png')} alt='' />
                <img className="lineTitleFX" src={require('../../assets/images/tour/forward.png')} alt='' />
            </div>
        );
    }

    renderDays() {
        return (
            <div>
                {
                    new Array(3).fill(0).map((item, index) => this.renderDaylayout(index))
                }
            </div>
        );
    }
    renderDaylayout(index) {
        return (
            <div>
                <div className="lineDayLayout">
                    <div className="lineDayIndex">第{index + 1}天</div>
                    <div className="lineDayAddressLoc">
                        <div className="lineDayAddress">贵阳</div>
                        <img className="lineDayLocation" src={require('../../assets/images/tour/line-loaction.png')} alt='' />
                    </div>
                    <div className="lineDayContent">
                        内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容
                        内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容
                    </div>
                </div>
                {
                    new Array(3).fill(0).map((item, index) => this.renderDayScenic())
                }
            </div>
        );
    }

    renderDayScenic() {
        return (
            <div className="lineDayScenicLayout">
                <div className="lineDayScenicHead">
                    <img className="lineDayScenicPic" src={require('../../assets/images/tour/line-park.png')} alt='' />
                    <div className="lineDayScenicHeadNameAndAddr">
                        <div className="lineDayScenicName">
                            <div style={{color:'#333333',fontSize:'18px',fontWeight:'700'}}>黔灵山公园</div>
                            <div style={{color:'#808080',fontSize:'15px',marginLeft:'5px'}}>喂猴子</div>
                        </div>
                        <div style={{color:'#808080',fontSize:'15px',marginTop:'5px',marginLeft:'15px'}}>贵阳市</div>
                    </div>
                    <div className="lineDayScenicspace"></div>
                    <img className="lineDayScenicGo" style={{marginRight:'10px'}} src={require('../../assets/images/tour/left_go.png')} alt='' />
                </div>
                {
                    this.renderBannerViews()
                }
                <div className="lineDayScenicContent">
                    内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容
                    内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容
                </div>
            </div>
        );
    }

    renderBannerViews()
    {
        let bannerList = [
            require('../../assets/images/home/banner1.png'),
            require('../../assets/images/home/banner2.png'),
            require('../../assets/images/home/banner3.png'),
        ];
        let dotStyle = {
            height: '6px',
            width: '6px',
            borderRadius: '3px',
            margin: '0 5px 10px 5px',
        };
        return (
            <Carousel
                className="lineScenics"
                autoplay={false}
                dots={true}
                dotStyle={{
                    ...dotStyle,
                    backgroundColor: '#ffffff',
                }}
                dotActiveStyle={{
                    ...dotStyle,
                    backgroundColor: '#00aae9',
                }}
                swiping={true}
                infinite
                selectedIndex={0}
                swipeSpeed={35}>
                {
                    bannerList.map((item, index)=><div key={index} className="lineScenicItem" style={{backgroundImage: `url(${item})`}}/>)
                }
            </Carousel>
        );
    }
    goBack() {
        // let dispatch = this.props.dispatch;
        this.props.history.goBack();
        // dispatch(collectGoBackAction());
    }
}

LineDetailScreen = connect()(LineDetailScreen);

export default LineDetailScreen;


