function _getListRequestAction(pageIndex, extraParams) {
    return {
        type: IConstant.Actions.TRAVELNOTE.LIST.GET_LIST_REQUEST,
        pageIndex,
        extraParams
    };
}

function _getListSuccessAction(pageIndex, result, extraParams) {
    return {
        type: IConstant.Actions.TRAVELNOTE.LIST.GET_LIST_SUCCESS,
        pageIndex,
        result,
        extraParams
    };
}

function _getListFailureAction(error, extraParams) {
    return {
        type: IConstant.Actions.TRAVELNOTE.LIST.GET_LIST_FAILURE,
        message: error,
        extraParams
    };
}



