function _getListRequestAction(pageIndex, orderFiled, extraParams) {
    return {
        type: IConstant.Actions.SMARTTOUR.ELEMENTLIST.GET_ELEMENTLIST_REQUEST,
        pageIndex,
        extraParams,
        orderFiled
    };
}

function _getListSuccessAction(pageIndex, orderFiled, extraParams, result) {
    return {
        type: IConstant.Actions.SMARTTOUR.ELEMENTLIST.GET_ELEMENTLIST_SUCCESS,
        pageIndex,
        extraParams,
        result,
        orderFiled
    };
}

function _getListFailureAction(orderFiled, extraParams, error) {
    return {
        type: IConstant.Actions.SMARTTOUR.ELEMENTLIST.GET_ELEMENTLIST_FAILURE,
        extraParams,
        message: error,
        orderFiled
    };
}


