function _getDetailRequestAction(id) {
    return {
        type: IConstant.Actions.ACTIVITY.DETAIL.GET_DETAIL_REQUEST,
        id,
    };
}

function _getDetailSuccessAction(id, resp) {
    return {
        type: IConstant.Actions.ACTIVITY.DETAIL.GET_DETAIL_SUCCESS,
        id,
        detailItem: resp
    };
}

function _getDetailFailureAction(id, error) {
    return {
        type: IConstant.Actions.ACTIVITY.DETAIL.GET_DETAIL_FAILURE,
        id,
        message: error
    };
}


