import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Toast, NavBar, Icon, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import ReactDOM from 'react-dom';
import HttpClient from '../../core/utils/HttpClient';
import SysUtil from '../../core/utils/SysUtil';

class TravelNoteEditScreen extends Component 
{
    constructor(props){
        super(props);
        this.state={
            title: '',
            content:'',
        };
    }
    render() {
        return (
            <div>
                <div className="travelNoteEdit">
                    <NavBar mode="dark"
                        className="navBar"
                        icon={<Icon type="left" />}
                        onLeftClick={() => {
                            this.props.history.goBack();
                        }}>
                        写游记
                    </NavBar>
                    <Content>
                        <div className="inputTitleDiv">
                            <input className="inputTitle" placeholder='点击添加标题'  onChange={(event)=>this.onTitleChange(event)}/>
                        </div>
                        <div className="addImage" onClick={this.addImage} >
                            <div className="addImageTip">封面图片</div>
                            <img className="addIcon" src={require('../../assets/images/travelnote/icon_add.png')} alt='' />
                        </div>
                        <div className="divContent">
                            <textarea className="inputContent" placeholder='点击添加内容...'  onChange={(event)=>this.onContentChange(event)}/>
                            <div className="divCamera">
                                <img className="cameraIcon" src={require('../../assets/images/travelnote/icon_camera.png')} alt='' />
                                <div className="wordNum">0/1000</div>
                            </div>
                        </div>
                        <div className="buttonBottom">
                            <div className="buttonSave">保存</div>
                            <div className="buttonSubmit">提交</div>
                        </div>
                    </Content>
                </div>
            </div>
        );
    }

    onTitleChange(event){
        this.setState({
            title:event.target.value
        });
    }

    onContentChange(event){
        this.setState({
            content:event.target.value
        });
    }

}
TravelNoteEditScreen = connect()(TravelNoteEditScreen);

export default TravelNoteEditScreen;


