import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import CheckBoxView from '../../views/tour/CheckBoxView';
import { asyncGetListAction, goBackAction } from '../../store/actions/preference/listAction';
import { asyncCreateTourAction } from '../../store/actions/create-tour/listAction';
import SysUtil from '../../core/utils/SysUtil';
import DateUtil from '../../core/utils/DateUtil';

class PreferenceScreen extends Component {

    constructor(props) {
        super(props);
        this.gotoStep1 = this.gotoStep1.bind(this);
        this.gotoStep2 = this.gotoStep2.bind(this);
        this.gotoStep3 = this.gotoStep3.bind(this);
        this.submit = this.submit.bind(this);
        this.state = {
            step: 1
        };
    }

    componentDidMount() {
        this.loadData();
    }

    loadData() {
        this.props.dispatch(asyncGetListAction(1)); //游玩偏好
        this.props.dispatch(asyncGetListAction(2)); //交通偏好
        this.props.dispatch(asyncGetListAction(3)); //酒店偏好
    }

    render() {
        return (
            <div className="preferenceScreen">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.props.history.goBack();
                    }}>
                    偏好设置
                </NavBar>
                <Content>
                    {
                        this.state.step === 1
                            ?
                            this.renderStep1Head()
                            : null
                    }
                    {
                        this.state.step === 2
                            ?
                            this.renderStep2Head()
                            : null
                    }
                    {
                        this.state.step === 3
                            ?
                            this.renderStep3Head()
                            : null
                    }
                    {
                        this.state.step === 1
                            ?
                            this.renderStep1()
                            : null
                    }
                    {
                        this.state.step === 2
                            ?
                            this.renderStep2()
                            : null
                    }
                    {
                        this.state.step === 3
                            ?
                            this.renderStep3()
                            : null
                    }
                </Content>
            </div>
        );
    }

    renderStep1Head() {
        return (
            <div className="perferenceStep">
                <div className="perferenceHead">
                    <div className="line1"></div>
                    <img className="perferencePlay" src={require('../../assets/images/tour/preference_play_on.png')} alt='' />
                    <div className="line2" style={{ backgroundColor: '#dddddd' }}></div>
                    <img className="perferenceTraffic" src={require('../../assets/images/tour/preference_traffic.png')} alt='' />
                    <div className="line3" style={{ backgroundColor: '#dddddd' }}></div>
                    <img className="perferenceHotel" src={require('../../assets/images/tour/preference_hotel.png')} alt='' />
                    <div className="line4" style={{ backgroundColor: '#dddddd' }}></div>
                </div>
                <div className="perferenceHeadDes">
                    <div className="des" style={{ color: '#6dd3ad' }}>游玩偏好</div>
                    <div className="des">交通偏好</div>
                    <div className="des">酒店偏好</div>
                </div>
            </div>
        );
    }
    renderStep2Head() {
        return (
            <div className="perferenceStep">
                <div className="perferenceHead">
                    <div className="line1"></div>
                    <img className="perferencePlay" src={require('../../assets/images/tour/preference_play_on.png')} alt='' />
                    <div className="line2" ></div>
                    <img className="perferenceTraffic" src={require('../../assets/images/tour/preference_traffic_on.png')} alt='' />
                    <div className="line3" style={{ backgroundColor: '#dddddd' }}></div>
                    <img className="perferenceHotel" src={require('../../assets/images/tour/preference_hotel.png')} alt='' />
                    <div className="line4" style={{ backgroundColor: '#dddddd' }}></div>
                </div>
                <div className="perferenceHeadDes">
                    <div className="des" style={{ color: '#6dd3ad' }}>游玩偏好</div>
                    <div className="des" style={{ color: '#6dd3ad' }}>交通偏好</div>
                    <div className="des">酒店偏好</div>
                </div>
            </div>
        );
    }
    renderStep3Head() {
        return (
            <div className="perferenceStep">
                <div className="perferenceHead">
                    <div className="line1"></div>
                    <img className="perferencePlay" src={require('../../assets/images/tour/preference_play_on.png')} alt='' />
                    <div className="line2" ></div>
                    <img className="perferenceTraffic" src={require('../../assets/images/tour/preference_traffic_on.png')} alt='' />
                    <div className="line3" ></div>
                    <img className="perferenceHotel" src={require('../../assets/images/tour/preference_hotel_on.png')} alt='' />
                    <div className="line4" style={{ backgroundColor: '#dddddd' }}></div>
                </div>
                <div className="perferenceHeadDes">
                    <div className="des" style={{ color: '#6dd3ad' }}>游玩偏好</div>
                    <div className="des" style={{ color: '#6dd3ad' }}>交通偏好</div>
                    <div className="des" style={{ color: '#6dd3ad' }}>酒店偏好</div>
                </div>
            </div>
        );
    }
    renderStep1() {
        let data;
        if (this.props.preference['1'] && this.props.preference['1'].dataList) {
            data = this.props.preference['1'].dataList;
            return (
                <div>
                    {
                        data.map((item, index) => (
                            <div>
                                <div className="title">{item.label}</div>
                                <CheckBoxView
                                    // ref={(view) => { this.checkBoxView1 = view; }}
                                    isSingleCheck={item.option === 'radio' ? true : false}
                                    data={item.options}
                                />
                            </div>
                        )
                        )
                    }
                    <div className="bottomBtn" onClick={this.gotoStep2}>下一步</div>
                </div>
            );
        } else {
            return null;
        }
    }

    renderStep2() {
        let data;
        if (this.props.preference['2'] && this.props.preference['2'].dataList) {
            data = this.props.preference['2'].dataList;

            return (
                <div>
                    {
                        data.map((item, index) => (
                            <div>
                                <div className="title">{item.label}</div>
                                <CheckBoxView
                                    // ref={(view) => { this.checkBoxView1 = view; }}
                                    isSingleCheck={item.option === 'radio' ? true : false}
                                    data={item.options}
                                />
                            </div>
                        )
                        )
                    }
                    <div className="bottom">
                        <div className="bottomBtn1" onClick={this.gotoStep1}>上一步</div>
                        <div className="bottomBtn2" onClick={this.gotoStep3}>下一步</div>
                    </div>
                </div>
            );
        } else {
            return null;
        }
    }

    renderStep3() {
        let data;
        if (this.props.preference['3'] && this.props.preference['3'].dataList) {
            data = this.props.preference['3'].dataList;

            return (
                <div>
                    {
                        data.map((item, index) => (
                            <div>
                                <div className="title">{item.label}</div>
                                <CheckBoxView
                                    // ref={(view) => { this.checkBoxView1 = view; }}
                                    isSingleCheck={item.option === 'radio' ? true : false}
                                    data={item.options}
                                />
                            </div>
                        )
                        )
                    }
                    <div className="bottom">
                        <div className="bottomBtn1" onClick={this.gotoStep2}>上一步</div>
                        <div className="bottomBtn2" onClick={this.submit}>完成</div>
                    </div>
                </div>
            );
        } else {
            return null;
        }
    }

    gotoStep1() {
        this.setState({
            step: 1
        });
        // console.log(this.checkBoxView1.getCheckedItems());
    }
    gotoStep2() {
        this.setState({
            step: 2
        });
    }
    gotoStep3() {
        this.setState({
            step: 3
        });
    }
    submit() {
        let startTime = this.props.match.params.startTime;
        let endTime = this.props.match.params.endTime;
        let startAddress = this.props.match.params.startAddress;
        let endAddress = this.props.match.params.endAddress;
        
        let days = DateUtil.GetDateDiff(startTime, endTime); 
        if(this.props.isUserLogined){
            let param = {
                'userId' : this.props.userInfo.uid,
                'departureLocality': startAddress,
                'destination': endAddress,
                'startTime': startTime,
                'endTime':endTime,
                'playDays': days,
                'routeName':'我的行程',                
            };
            let data1 = this.props.preference['1'].dataList;
            for(let i=0;i<data1.length;i++){
                let element = data1[i];
                let key = element.key;
                let isCheckedStr = '';
                if (element.options) {
                    for (let j = 0; j < element.options.length; j++) {
                        if(element.options[j].isCheck){
                            isCheckedStr = isCheckedStr + element.options[j].ZDXLBH + ',';
                        }
                    } 
                    isCheckedStr = isCheckedStr.substring(0, isCheckedStr.lastIndexOf(','));  
                }
                param[key] = isCheckedStr;
            }

            let data2 = this.props.preference['2'].dataList;
            for(let i=0;i<data2.length;i++){
                let element = data2[i];
                let key = element.key;
                let isCheckedStr = '';
                if (element.options) {
                    for (let j = 0; j < element.options.length; j++) {
                        if(element.options[j].isCheck){
                            isCheckedStr = isCheckedStr + element.options[j].ZDXLBH + ',';
                        }
                    } 
                    isCheckedStr = isCheckedStr.substring(0, isCheckedStr.lastIndexOf(','));  
                }
                param[key] = isCheckedStr;
            }

            let data3 = this.props.preference['3'].dataList;
            for(let i=0;i<data3.length;i++){
                let element = data3[i];
                let key = element.key;
                let isCheckedStr = '';
                if (element.options) {
                    for (let j = 0; j < element.options.length; j++) {
                        if(element.options[j].isCheck){
                            isCheckedStr = isCheckedStr + element.options[j].ZDXLBH + ',';
                        }
                    } 
                    isCheckedStr = isCheckedStr.substring(0, isCheckedStr.lastIndexOf(','));  
                }
                param[key] = isCheckedStr;
            }
            console.log(param);
            this.props.dispatch(asyncCreateTourAction(param)).then(()=>{
                if(this.props.createTour.status && this.props.createTour.status === '0'){
                    this.props.dispatch(goBackAction());
                    this.props.history.push('/smart-tour/tourDetail');
                }else{
                    Toast.fail('行程创建失败', 1);                    
                }
            });
        }else{
            this.props.history.push('/me/login');
        }
        
    }
}

const mapStateToProps = function (state) {
    return {
        userInfo: SysUtil.getUserInfo(state),
        isUserLogined: SysUtil.isUserLogined(state), 
        preference: state.preference.listScreen.groups,
        createTour: state.createTour.listScreen.groups,
    };
};

PreferenceScreen = connect(mapStateToProps)(PreferenceScreen);

export default PreferenceScreen;


