import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

//智能创建行程
export function asyncCreateTourAction(param) {
    return function (dispatch, getState) {
        dispatch(_getListRequestAction());
        return _fetchList(param).then((resp) => {
            dispatch(_getListSuccessAction(resp));
        }).catch((error) => {
            dispatch(_getListFailureAction(error));
        });
    };
}

// export function goBackAction() {
//     return {
//         type: IConstant.Actions.PREFERENCR.LIST.GO_BACK,
//     };
// }

function _fetchList(param) {
    let url = HttpClient.HOST_MAIN + '/mobile/WisdomStroke.do';
    let kvDict = param;

    return HttpClient.getData(url, kvDict).then((resp) => resp.json());
}

function _getListRequestAction() {
    return {
        type: IConstant.Actions.CREATE_TOUR.SMART.GET_REQUEST,
    };
}

function _getListSuccessAction(result) {
    return {
        type: IConstant.Actions.CREATE_TOUR.SMART.GET_SUCCESS,
        result,
    };
}

function _getListFailureAction(error) {
    return {
        type: IConstant.Actions.CREATE_TOUR.SMART.GET_FAILURE,
        message: error,
    };
}

//自主创建行程
export function asyncSelfCreateTourAction(param) {
    return function (dispatch, getState) {
        dispatch(_getSelfListRequestAction());
        return _fetchSelfList(param).then((resp) => {
            dispatch(_getSelfListSuccessAction(resp));
        }).catch((error) => {
            dispatch(_getSelfListFailureAction(error));
        });
    };
}

function _fetchSelfList(param) {
    let url = HttpClient.HOST_MAIN + '/mobile/queryOwnRoute.do';
    let kvDict = param;

    return HttpClient.getData(url, kvDict).then((resp) => resp.json());
}

function _getSelfListRequestAction() {
    return {
        type: IConstant.Actions.CREATE_TOUR.SMART.GET_REQUEST,
    };
}

function _getSelfListSuccessAction(result) {
    return {
        type: IConstant.Actions.CREATE_TOUR.SMART.GET_SUCCESS,
        result,
    };
}

function _getSelfListFailureAction(error) {
    return {
        type: IConstant.Actions.CREATE_TOUR.SMART.GET_FAILURE,
        message: error,
    };
}


