import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import { 
    asyncGetDetailAction, 
    goBackAction, 
} from '../../store/actions/activity/detailAction';

class ActivityDetailScreen extends Component
{
    constructor(props){
        super(props);
        this.handleScroll = this.handleScroll.bind(this);
    }
    componentDidMount()
    {
        this.loadData();
    }
    handleScroll(){
        console.log('=====================');
    }
    render()
    {
        return (
            <div className="activity">
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    rightContent={this.renderRightMenu()}                   
                    onLeftClick={() => {
                        let id = this.props.match.params.id;
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction(id));
                    }}>
                    活动详情
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view;}} >
                    {
                        this.renderContentView()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderRightMenu() {
        return (
            <div className="informationTitleRight">
                <img className="informationTitleXX" src={require('../../assets/images/information/icon_collect.png')} alt='' />
                <img className="informationTitleFX" src={require('../../assets/images/information/icon_share.png')} alt='' />
            </div>
        );
    }
    
    renderContentView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div className="activityDetailContentView" onScroll={this.handleScroll}>
                <div className="activityDetailHeaderImg" style={{backgroundImage: `url(${this.props.detailItem.F_MiddleImage})`}} >
                </div>
                <div className="activityDetailContent" >
                    <div className="activityDetailTitle" >{this.props.detailItem.Head}</div>
                    <div className="activityDetailDays" >距活动结束还有2天</div>
                    <div className="activityDetailTimeDiv" >
                        <img className="activityDetailTimeIcon" src={require('../../assets/images/activity/icon_hd_s.png')} alt='' />
                        <div className="activityDetailTime" >{this.props.detailItem.BeginDate}</div>
                    </div>
                    <div className="activityDetailTimeDiv" >
                        <img className="activityDetailTimeIcon" src={require('../../assets/images/activity/icon_hd_d.png')} alt='' />
                        <div className="activityDetailTime" >{this.props.detailItem.Address}</div>
                    </div>
                    <div className="activityDetailTimeDiv" >
                        <img className="activityDetailTimeIcon" src={require('../../assets/images/activity/icon_hd_r.png')} alt='' />
                        <div className="activityDetailTime" >{this.props.detailItem.Organizer}</div>
                    </div>
                    <div className="activityDetailTimeDiv" >
                        <img className="activityDetailTimeIcon" src={require('../../assets/images/activity/icon_hd_dh.png')} alt='' />
                        <div className="activityDetailTime" >0724-123456</div>
                    </div>
                    <div className="activityDetailSplitLine"/>
                    <div className="activityDetailDescribes" >{this.props.detailItem.Describes}{this.props.detailItem.Describes}</div>
                    <div className="activityDetailSign">报名</div>
                </div>
            </div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetDetailAction(this.props.match.params.id));
    }
    
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.activity.detailScreen[ownProps.match.params.id];
    return {
        ...obj,
    };
};

ActivityDetailScreen = connect(mapStateToProps)(ActivityDetailScreen);

export default ActivityDetailScreen;


