function _getListRequestAction(key, pageIndex, extraParams) {
    return {
        type: IConstant.Actions.ACTIVITY.LIST.GET_LIST_REQUEST,
        key,
        pageIndex,
        extraParams
    };
}

function _getListSuccessAction(key, pageIndex, result, extraParams) {
    return {
        type: IConstant.Actions.ACTIVITY.LIST.GET_LIST_SUCCESS,
        key,
        pageIndex,
        result,
        extraParams
    };
}

function _getListFailureAction(key, error, extraParams) {
    return {
        type: IConstant.Actions.ACTIVITY.LIST.GET_LIST_FAILURE,
        key,
        message: error,
        extraParams
    };
}


