function _getListRequestAction(key, pageIndex) {
    return {
        type: IConstant.Actions.STRATEGY.LIST.GET_LIST_REQUEST,
        key,
        pageIndex,
    };
}

function _getListSuccessAction(key, pageIndex, result) {
    return {
        type: IConstant.Actions.STRATEGY.LIST.GET_LIST_SUCCESS,
        key,
        pageIndex,
        result
    };
}

function _getListFailureAction(key, error) {
    return {
        type: IConstant.Actions.STRATEGY.LIST.GET_LIST_FAILURE,
        key,
        message: error
    };
}


