import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import { goBackAction } from '../../store/actions/me/travelNotelistAction';
import '../../assets/css/travelNotes.css';
import TravelNotesListView from '../../views/travel-notes/TravelNotesListView';
import { SpecialStoreKey } from '../../core/utils/IConstant';
import SysUtil from '../../core/utils/SysUtil';
/**
 * 游记
 */
class MyTravelNoteScreen extends Component {
    constructor(props) {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }

    render() {
        let listViewHeight = window.innerHeight - (45 + 44);
        return (
            <div className="informationScreen">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction(this.getExtraParams()));
                    }}>
                    我的游记
                </NavBar>
                <Content ref={(view) => { this.scrollView = view; }} heightOffset={-50}>
                    <TravelNotesListView
                        ref={(listView) => {
                            if (listView) {
                                this.listView = listView.getWrappedInstance();
                            }
                        }}
                        onItemClick={this.onItemClick}
                        height={listViewHeight}
                        storeKey={SpecialStoreKey.ME_TRAVEL_NOTE}
                    />
                </Content>
            </div>
        );
    }

    onItemClick(item) {
        if (!item) {
            return;
        }
        let listView: TravelNotesListView = this.listView;
        if (listView) {
            listView.cacheScrollOffset();
        }

        this.props.history.push(`/travelNotes/detail/${item.ID}`);
    }

    getExtraParams()
    {
        return {
            storeKey: SpecialStoreKey.ME_TRAVEL_NOTE,
        };
    }
}

const mapStateToProps = function (state, ownProps) {
    let obj = state.me.travelNotelistScreen.groups;
    return {
        ...obj,
    };
};

MyTravelNoteScreen = connect(mapStateToProps)(MyTravelNoteScreen);

export default MyTravelNoteScreen;


