import React, { Component } from 'react';
import { connect } from 'react-redux';

class TourBudgetHotelView extends Component
{
    constructor(props)
    {
        super(props);
    }
    componentDidMount()
    {
        
    }
    render()
    {
        let hotel_icon=require('../../assets/images/tour/budget_hote_icon.png');
        let imgUrl='http://103.10.86.241:8081/Attachment/default/Small/2015/7/8/DSC04708201507081526328073234.JPG';

        return (
            <div className='budgetHotelViewBox'>
                <div className='budgetHotelViewHead'>
                    <img src={hotel_icon} alt='' />
                    <span>贵阳海亿商务酒店</span>
                </div>
                <div className='budgetHotelViewImg' style={{backgroundImage:`url(${imgUrl})`}}></div>
                <div className='budgetHotelRoomInfo'>
                    <div className='infoRow'>
                        <div className='roomNumber'>标准单间 1间</div>
                        <div className='roomSingPrice'>单价：&yen;130</div>
                    </div>
                    <div className='infoRow'>
                        <div className='roomStartTime'>入住 2017-08-01</div>
                        <div className='roomStayDays'>1晚</div>
                        <div className='roomEndTime'>退房 2017-08-02</div>
                    </div>
                </div>
                <div className='budgetHotelSumPrice'>总价：&yen;138</div>
            </div>
        );
    }
}

const mapStateToProps = function(state, ownProps)
{
    return {
    
    };
};

TourBudgetHotelView = connect(mapStateToProps, null, null, {withRef: true})(TourBudgetHotelView);

export default TourBudgetHotelView;


