import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function setTypeIndexAction(index) {
    return {
        type: IConstant.Actions.PREFERENCR.LIST.SET_TYPE_INDEX,
        index: index,
    };
}

export function setCheckStatusChange(){
    return {
        type : IConstant.Actions.PREFERENCR.LIST.CHANGE_CHECK_STATUS,
    };
}

export function asyncGetListAction(key) {
    return function (dispatch, getState) {
        dispatch(_getListRequestAction(key));
        return _fetchList(key).then((resp) => {
            dispatch(_getListSuccessAction(key, resp));
        }).catch((error) => {
            dispatch(_getListFailureAction(key, error));
        });
    };
}

export function goBackAction() {
    return {
        type: IConstant.Actions.PREFERENCR.LIST.GO_BACK,
    };
}

function _fetchList(key) {
    let url = HttpClient.HOST_MAIN + '/mobile/queryPreferencesByType.do';
    let kvDict = {
        type: key,
    };

    return HttpClient.getData(url, kvDict).then((resp) => resp.json());
}

function _getListRequestAction(key) {
    return {
        type: IConstant.Actions.PREFERENCR.LIST.GET_LIST_REQUEST,
        key,
    };
}

function _getListSuccessAction(key, result) {
    return {
        type: IConstant.Actions.PREFERENCR.LIST.GET_LIST_SUCCESS,
        key,
        result,
    };
}

function _getListFailureAction(key, error) {
    return {
        type: IConstant.Actions.PREFERENCR.LIST.GET_LIST_FAILURE,
        key,
        message: error,
    };
}


