import { Actions } from '../../../core/utils/IConstant';

export default function listReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.ELEMENTMAP.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            isFetching: true,
            pageIndex: action.pageIndex,
        };
    case Actions.ELEMENTMAP.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : state.dataList;
        dataList = dataList ? dataList : [];
        dataList = dataList.concat(action.result.data.dataList);
        return {
            ...state,
            isFetching: false,
            dataList: dataList,
            total: action.result.data.total,
            pageIndex: action.pageIndex,
            hasMore: (action.result.data.total > dataList.length)
        };
    case Actions.ELEMENTMAP.LIST.GET_LIST_FAILURE:
        return {
            isFetching: false,
        };
    case Actions.ELEMENTMAP.LIST.GO_BACK:
        return {};
    case Actions.ELEMENTMAP.LIST.DELETE:
        return {
            isFetching: false,
            dataList: [],
        };
    case Actions.ELEMENTMAP.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            listViewOffset: action.listViewOffset
        };
    default:
        return state;
    }
}


