import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Carousel, PullToRefresh } from 'antd-mobile';
import Content from '../../views/common/Content';
import { asyncGetListAction, goBackAction, asyncDeleteListAction } from '../../store/actions/information/listAction';
import '../../assets/css/information.css';
import InformationListView from '../../views/information/InformationListView';
/**
 * 资讯
 */
class InformationListScreen extends Component {
    constructor(props) {
        super(props);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadListData = this.loadListData.bind(this);
        this.onItemClick = this.onItemClick.bind(this);
    }

    render() {
        let listViewHeight = window.innerHeight - 45;

        return (
            <div className="informationScreen">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    资讯
                </NavBar>
                <Content ref={(view) => { this.scrollView = view; }} >
                    <PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}>
                        {
                            this.renderBannerViews()
                        }
                        <InformationListView
                            onItemClick={this.onItemClick}
                            height={listViewHeight}
                        />
                    </PullToRefresh>
                </Content>
            </div>
        );
    }

    renderBannerViews() {
        let bannerList = [
            require('../../assets/images/home/banner1.png'),
            require('../../assets/images/home/banner2.png'),
            require('../../assets/images/home/banner3.png'),
        ];
        let dotStyle = {
            height: '8px',
            width: '8px',
            borderRadius: 8,
            margin: '0 5px 5px 5px',
        };
        return (
            <Carousel
                className="banners"
                autoplay={true}
                dots={true}
                dotStyle={{
                    ...dotStyle,
                    backgroundColor: '#d9d9d9',
                }}
                dotActiveStyle={{
                    ...dotStyle,
                    backgroundColor: '#fc4043',
                }}
                swiping={true}
                infinite
                selectedIndex={0}
                swipeSpeed={35}>
                {
                    bannerList.map((item, index) => <div key={index}
                        className="bannerItem"
                        style={{ backgroundImage: `url(${item})` }}>
                        <div className="informationBannerItemBottom">
                            <div className="informationBannerItemText">走进安顺旅游体验活动开营</div>
                        </div>
                    </div>)
                }
            </Carousel>
        );
    }

    onRefresh() {
        this.props.dispatch(asyncDeleteListAction()).then(() => {
            // this.loadListData();
            let pageIndex = 1;
            this.props.dispatch(asyncGetListAction(pageIndex));
        });
    }

    loadListData() {
        if (this.props.isFetching) {
            return;
        }
        if (this.props.hasMore && this.props.hasMore === false) {
            return;
        }

        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        this.props.dispatch(asyncGetListAction(pageIndex));
    }
    onItemClick(item) {
        if (!item) {
            return;
        }
        let listView: InformationListView = this.listView;
        if (listView) {
            listView.cacheScrollOffset();
        }

        this.props.history.push(`/information/detail/${item.ID}`);
    }
}

const mapStateToProps = function (state, ownProps) {
    let obj = state.information.listScreen.groups;
    return {
        ...obj,
    };
};

InformationListScreen = connect(mapStateToProps)(InformationListScreen);

export default InformationListScreen;


