import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncGetMeListAction, asyncDeleteListAction, setScrollerPositionAction } from '../../store/actions/travel-notes/listAction';
import UIUtil from '../../core/utils/UIUtil';
import SysUtil from '../../core/utils/SysUtil';
import HttpClient from '../../core/utils/HttpClient';
import { SpecialStoreKey } from '../../core/utils/IConstant';
import EmptyView from '../../views/common/EmptyView';

class TravelNotesListView extends Component {
    constructor(props) {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);

        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount() {
        // 处理返回
        if (!this.props.dataList) {
            this.loadData();
        }
        if (this.listView && !isNaN(this.props.listViewOffset)) {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentWillReceiveProps(nextProps) {
        if (this.props.dataList !== nextProps.dataList) {
            this.setState({ dataSource: this.state.dataSource.cloneWithRows(nextProps.dataList ? nextProps.dataList : []) });
        }
    }
    render() {
        return (
            <div className="listBox" style={{ height: this.props.height }}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh()}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator
                            toast
                            text="Loading..." />
                        : null
                }
            </div>
        );
    }
    renderListView() {
        if (this.props.dataList && this.props.dataList.length > 0) {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );

            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}
                    />}
                    onEndReached={this.loadNextPage}                    
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50} />
            );
        }
    }
    isEmptyString(str) {
        return !str || str.length === 0;
    }
    onRefresh() {
        this.props.dispatch(asyncDeleteListAction(this.getExtraParams())).then(() => {
            // this.loadData();
            let pageIndex = 1;
            if(this.props.storeKey === SpecialStoreKey.ME_TRAVEL_NOTE){
                if(this.props.isUserLogined){
                    this.props.dispatch(asyncGetMeListAction(pageIndex,this.getExtraParams(),this.props.userInfo.uid));                    
                }
            }else{
                this.props.dispatch(asyncGetListAction(pageIndex,this.getExtraParams()));                
            }
        });
    }
    loadNextPage() {
        this.loadData();
    }
    renderFooter() {
        if (!this.props.isFetching || this.props.hasMore !== true) {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID) {
        let ImgBg = HttpClient.HOST_MAIN +  item.MFRONTURL;
        let ImgHead = HttpClient.HOST_MAIN +  item.HEADURL;
        
        return (
            <div className="travelNotesListItem">
                <div key={rowID}
                    className="travelNotesListItemBg"
                    style={{ backgroundImage: `url(${ImgBg})` }}
                    onClick={() => {
                        if (this.props.onItemClick) {
                            this.props.onItemClick(item);
                        }
                    }}>
                    <div className="travelNotesListItemBottom">
                        <div className="travelNotesListItemLeft">
                            <div className="travelNotesListItemTitle">
                                {item.title}
                            </div> 
                            <div className="travelNotesListItemTC">
                                <div className="travelNotesListItemTime">
                                    {item.CreateDate}
                                </div>
                                <div className="travelNotesListItemCount">
                                    <img className="travelNotesListItemCountImg" src={require('../../assets/images/information/icon_yj_y.png')} alt='' />
                                    <div className="travelNotesListItemCountNum">{item.ClickCount}</div>
                                </div>
                            </div>
                        </div> 
                        <div className="travelNotesListItemRight">
                            <div className="travelNotesListItemUserImg" style={{ backgroundImage: `url(${ImgHead})` }} />
                            <div className="travelNotesListItemUserName">{item.YHXM}</div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
    loadData() {
        if (this.props.isFetching) {
            return;
        }
        if (this.props.hasMore && this.props.hasMore === false) {
            return;
        }

        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        if(this.props.storeKey === SpecialStoreKey.ME_TRAVEL_NOTE){
            if(this.props.isUserLogined){
                this.props.dispatch(asyncGetMeListAction(pageIndex,this.getExtraParams(),this.props.userInfo.uid));                    
            }
        }else{
            this.props.dispatch(asyncGetListAction(pageIndex,this.getExtraParams()));                
        }
    }
    
    cacheScrollOffset() {
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(offsetY, this.getExtraParams()));
    }

    getExtraParams()
    {
        return {
            storeKey: this.props.storeKey,
        };
    }
}

TravelNotesListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

const mapStateToProps = function (state, ownProps) {
    let storeItem = (ownProps.storeKey && ownProps.storeKey.length > 0) ? SysUtil.getStoreItemByDotConnector(state, ownProps.storeKey) : state.travelNotes.listScreen;    
    let obj = storeItem.groups;
    return {
        userInfo: SysUtil.getUserInfo(state),
        isUserLogined: SysUtil.isUserLogined(state), 
        ...obj,
    };
};

TravelNotesListView = connect(mapStateToProps, null, null, { withRef: true })(TravelNotesListView);

export default TravelNotesListView;


