import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function selectedTabIndexReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.SMARTTOUR.ELEMENTLIST.SET_SELECTED_TAB_INDEX:
        return action.index;
    case Actions.SMARTTOUR.ELEMENTLIST.GO_BACK:
        return 0;
    default:
        return state;
    }
}

function elementSelectReducer(state = [], action)
{
    let scenicCodes = state.scenicCodes ? state.scenicCodes : [];
    let foodCodes = state.foodCodes ? state.foodCodes : [];
    let shopCodes = state.shopCodes ? state.shopCodes : [];
    let funCodes = state.funCodes ? state.funCodes : [];
    let hotelCodes = state.hotelCodes ? state.hotelCodes : [];
    
    if(action.tabs === 0){
        // let arr=state;
        // arr=arr?arr:[];
        // if (action.isAdd) {
        //     arr.push(action.code);
        // } else {
        //     for(let i=0;i<arr.length;i++){
        //         if(arr[i]===action.code){
        //             arr.splice(i,1);
        //             break;
        //         }
        //     }
        // }
        if (action.isAdd) {
            scenicCodes.push(action.code);
        } else {
            for(let i=0;i<scenicCodes.length;i++){
                if(scenicCodes[i] === action.code){
                    scenicCodes.splice(i,1);
                    break;
                }
            }
        }
    }else if(action.tabs === 1){
        if (action.isAdd) {
            foodCodes.push(action.code);
        } else {
            for(let i=0;i<foodCodes.length;i++){
                if(foodCodes[i] === action.code){
                    foodCodes.splice(i,1);
                    break;
                }
            }
        }
    }else if(action.tabs === 2){
        if (action.isAdd) {
            shopCodes.push(action.code);
        } else {
            for(let i=0;i<shopCodes.length;i++){
                if(shopCodes[i] === action.code){
                    shopCodes.splice(i,1);
                    break;
                }
            }
        }
    }else if(action.tabs === 3){
        if (action.isAdd) {
            funCodes.push(action.code);
        } else {
            for(let i=0;i<funCodes.length;i++){
                if(funCodes[i] === action.code){
                    funCodes.splice(i,1);
                    break;
                }
            }
        }
    }else if(action.tabs === 4){
        if (action.isAdd) {
            hotelCodes.push(action.code);
        } else {
            for(let i=0;i<hotelCodes.length;i++){
                if(hotelCodes[i] === action.code){
                    hotelCodes.splice(i,1);
                    break;
                }
            }
        }
    }
    switch(action.type)
    {
    case Actions.SMARTTOUR.ELEMENTLIST.ADD_ELEMENT_SELECT:
        // scenicCodes = [].concat(scenicCodes);
        return {
            scenicCodes:scenicCodes,
            foodCodes:foodCodes,
            shopCodes:shopCodes,
            funCodes:funCodes,
            hotelCodes:hotelCodes
        };
    case Actions.SMARTTOUR.ELEMENTLIST.GO_BACK:
        return [];
    default:
        return state;
    }
}

function groupReducer(state = {}, action)
{
    // 可能是我的周边列表/景区周边列表
    // if(action.extraParams && action.extraParams.storeKey && action.extraParams.storeKey.length > 0)
    // {
    //     return {...state};
    // }
    let obj = state[action.orderFiled];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.SMARTTOUR.ELEMENTLIST.GET_ELEMENTLIST_REQUEST:
        return {
            ...state, 
            [action.orderFiled]: {
                ...obj,
                isFetching: true,
                pageIndex: action.pageIndex,
            }
        };
    case Actions.SMARTTOUR.ELEMENTLIST.GET_ELEMENTLIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        dataList = dataList.concat(action.result.data);
        return {
            ...state,
            [action.orderFiled]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
                total: action.result.total,
                pageIndex: action.pageIndex,
                hasMore: (action.result.total > dataList.length)
            }
        };
    case Actions.SMARTTOUR.ELEMENTLIST.GET_ELEMENTLIST_FAILURE:
        return {
            ...state,
            [action.orderFiled]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.SMARTTOUR.ELEMENTLIST.GO_BACK:
        return {};
    case Actions.SMARTTOUR.ELEMENTLIST.COLLECT_GO_BACK:
        let _state = {...state};
        delete _state['collect'];
        return {
            ..._state
        };
    case Actions.SMARTTOUR.ELEMENTLIST.DELETE:
        return {
            ...state,
            [action.orderFiled]: {
                isFetching: false,
                dataList: [],
            }
        };
    case Actions.SMARTTOUR.ELEMENTLIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [action.orderFiled]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    default:
        return state;
    }
}

const elementlistReducer = combineReducers({
    selectedTabIndex: selectedTabIndexReducer,
    selectCodes:elementSelectReducer,
    groups: groupReducer,
});

export default elementlistReducer;


