import { Actions } from '../../../core/utils/IConstant';

export default function homeReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.HOME.SET_SCROLL_OFFSET:
        return {
            ...state, 
            scrollViewOffset: action.scrollViewOffset,
        };
    case Actions.HOME.SET_SELECTED_TAB_INDEX:
        return {
            ...state,
            selectedTabIndex: action.index,
        };
    default:
        return state;
    }
}


