import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Toast, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import CommentList from '../../views/information/CommentListView';
import HttpClient from '../../core/utils/HttpClient';
import SysUtil from '../../core/utils/SysUtil';

class CommentListScreen extends Component {
    constructor(props){
        super(props);
        this.commit = this.commit.bind(this);
        this.state={
            commitContent: '',
        };
    }
    render() {
        let listViewHeight = window.innerHeight - (45 + 44);
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        // let id = this.props.match.params.id;
                        // let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        // dispatch(goBackAction(id));
                    }}>
                    评论
                </NavBar>
                <Content>
                    <CommentList
                        ref={(view) => { this.commitListView = view; }}
                        id={this.props.match.params.id}
                        height={listViewHeight}
                        type={this.props.match.params.type} />
                    {
                        this.renderCommentInputView()
                    }
                </Content>
            </div>
        );
    }

    renderCommentInputView() {
        return (
            <div className="informationCommentBottom">
                <div className="informationCommentBottomLeft">
                    <img className="informationCommentFB" src={require('../../assets/images/information/icon_fb1.png')} alt='' />
                    <input className="informationCommentInput" placeholder="写评论" onChange={(event)=>this.commitChange(event)}/>
                </div>
                <div className="informationCommentBottomRight" onClick={this.commit}>评论</div>
            </div>
        );
    }

    commitChange(event){
        this.setState({ commitContent : event.target.value });
    }

    commit(){
        if(this.state.commitContent.length === 0){
            Toast.info('请输入您要评论的内容');
            return;
        }
        if (this.props.isUserLogined) {
            if(this.props.match.params.type === 'travelNotes'){ //游记评论
                let url = HttpClient.HOST_MAIN + '/mobile/addComment.do';
                let kvDict = {
                    userId: this.props.userInfo.uid,
                    Content:this.state.commitContent,
                    TableCode: this.props.match.params.id,
                };
                return HttpClient.getData(url, kvDict).then((resp) => resp.json())
                    .then((resp) => {
                        if(resp.status === '0'){
                            Toast.success('评论成功', 1);    
                            // this.commitListView.loadData();                
                        }else{
                            Toast.fail(resp.Msg, 1);                                        
                        }
                    });
            }else if(this.props.match.params.type === 'information'){//资讯评论
                let url = HttpClient.HOST_MAIN + '/mobile/addInfoComment.do';
                let kvDict = {
                    userId: this.props.userInfo.uid,
                    Content:this.state.commitContent,
                    TableCode: this.props.match.params.id,
                };
                return HttpClient.getData(url, kvDict).then((resp) => resp.json())
                    .then((resp) => {
                        if(resp.status === '0'){
                            Toast.success('评论成功', 1);    
                            // this.commitListView.loadData();                
                        }else{
                            Toast.fail(resp.Msg, 1);                                        
                        }
                    });
            }
            
        }else{
            this.props.history.push('/me/login');
        }
        
    }
}
const mapStateToProps = function (state, ownProps) {
    return {
        userInfo: SysUtil.getUserInfo(state),
        isUserLogined: SysUtil.isUserLogined(state),        
    };
};

CommentListScreen = connect(mapStateToProps)(CommentListScreen);

export default CommentListScreen;


