import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import ActivityListView from '../../views/activity/ActivityListView';
import { goBackAction } from '../../store/actions/me/activitylistAction';
import '../../assets/css/activity.css';
import { SpecialStoreKey } from '../../core/utils/IConstant';

class MyActivityListScreen extends Component {
    constructor(props) {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }

    render() {
        let listViewHeight = window.innerHeight - (45 + 44);
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction(this.getExtraParams()));
                    }}>
                    我的活动
                </NavBar>
                <Content>
                    <ActivityListView
                        type={3}
                        ref={(listView) => {
                            if (listView) {
                                this.listView = listView.getWrappedInstance();
                            }
                        }}
                        storeKey={SpecialStoreKey.ME_ACTIVITY}
                        onItemClick={this.onItemClick}
                        height={listViewHeight} />
                </Content>
            </div>
        );
    }
    onItemClick(item) {
        if (!item) {
            return;
        }
        let listView: ActivityListView = this.listView;
        if (listView) {
            listView.cacheScrollOffset();
        }

        this.props.history.push(`/activity/detail/${item.ContentID}`);
    }

    getExtraParams() {
        return {
            storeKey: SpecialStoreKey.ME_ACTIVITY,
        };
    }

}

MyActivityListScreen = connect()(MyActivityListScreen);

export default MyActivityListScreen;


