import React, { Component } from 'react';
import ReactDOM from 'react-dom';

export default class Content extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            height: 100,
        };
    }
    render()
    {
        return (
            <div className="appContent" style={{height: (this.state.height + 'px'), ...this.props.style}}>{this.props.children}</div>
        );
    }
    componentDidMount()
    {
        let height = window.innerHeight - ReactDOM.findDOMNode(this).parentNode.offsetTop;
        if(this.props.heightOffset)
        {
            height += this.props.heightOffset;
        }
        this.setState({height: height});
    }
    getOffsetY()
    {
        return ReactDOM.findDOMNode(this).scrollTop;
    }
    scrollToY(offsetY)
    {
        // ReactDOM.findDOMNode(this).scrollTo(0, offsetY);
        ReactDOM.findDOMNode(this).scrollTop = offsetY;        
    }
}



