import { Actions } from '../../../core/utils/IConstant';
import travelNotelistReducer from './travelNotelistReducer';
import activitylistReducer from './activitylistReducer';
import collectReducer from './collectReducer';
import userInfoReducer from './userInfoReducer';

import { combineReducers } from 'redux';

const mineReducer = combineReducers({
    travelNotelistScreen: travelNotelistReducer,
    activitylistScreen: activitylistReducer,
    meScreen: meReducer,
    collect: collectReducer,
    userInfo:userInfoReducer
});

function meReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.ME.SET_SCROLL_OFFSET:
        return {
            ...state, 
            scrollViewOffset: action.scrollViewOffset,
        };
    default:
        return state;
    }
}

export default mineReducer;


