import * as IConstant from '../../../core/utils/IConstant';

export function goBackActivityAction(extraParams)
{
    return {
        type: IConstant.Actions.ME.COLLECT.ACTIVITY_GO_BACK,
        extraParams
    };
}

export function goBackInformationAction(extraParams)
{
    return {
        type: IConstant.Actions.ME.COLLECT.INFORMATION_GO_BACK,
        extraParams
    };
}

export function goBackTravelNoteAction(extraParams)
{
    return {
        type: IConstant.Actions.ME.COLLECT.TRAVELNOTE_GO_BACK,
        extraParams
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.ME.COLLECT.GO_BACK,
    };
}

export function setSelectedTabIndexAction(index)
{
    return {
        type: IConstant.Actions.ME.COLLECT.SET_SELECTED_TAB_INDEX,
        index: index
    };   
}


