import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function asyncGetListAction(pageIndex) {
    return function (dispatch, getState) {
        dispatch(_getListRequestAction(pageIndex));
        return _fetchList(pageIndex).then((resp) => {
            dispatch(_getListSuccessAction(pageIndex, resp));
        }).catch((error) => {
            dispatch(_getListFailureAction(error));
        });
    };
}

export function goBackAction() {
    return {
        type: IConstant.Actions.SMARTTOUR.TOURLIST.GO_BACK,
    };
}

export function asyncDeleteListAction() {
    return function (dispatch, getState) {
        dispatch(deleteListAction());
        return new Promise((resolve) => {
            resolve();
        });
    };
}

export function deleteListAction() {
    return {
        type: IConstant.Actions.SMARTTOUR.TOURLIST.DELETE,
    };
}

export function setScrollerPositionAction(offset) {
    return {
        type: IConstant.Actions.SMARTTOUR.TOURLIST.SET_LISTVIEW_OFFSET,
        listViewOffset: offset,
    };
}

function _fetchList(pageIndex) {
    // let url = HttpClient.HOST_MAIN + '/mobile/ActivityNote.do';
    // let kvDict = {
    //     curPage: pageIndex,
    //     pageSize: IConstant.PAGE_SIZE,
    // };

    // return HttpClient.getData(url, kvDict).then((resp) => resp.json());

    return new Promise((resolve, reject) => {
        setTimeout(() => {
            let list = {
                'status': '0',
                'message': '结果返回成功！',
                'total': 3,
                'data': [
                    {
                        'code':'111',
                        'days': 1,
                        'imageUrl': 'http://test.hbysp.cn/Attachment/default/Source/2017/5/19/201705191820465279994.jpg',
                        'title': '贵阳一日游',
                        'playDate': '2017-10-10'
                    },
                    {
                        'code':'222',                        
                        'days': 2,
                        'imageUrl': 'http://test.hbysp.cn/Attachment/default/Source/2017/5/19/201705191820465279994.jpg',
                        'title': '贵阳两日游',
                        'playDate': '2017-11-20'                        
                    },
                    {
                        'code':'333',                        
                        'days': 3,
                        'imageUrl': 'http://test.hbysp.cn/Attachment/default/Source/2017/5/19/201705191820465279994.jpg',
                        'title': '贵阳三日游',
                        'playDate': '2017-12-20'                                                
                    }
                ]
            };
            resolve(list);
        }, 200);
    });
}

function _getListRequestAction(pageIndex) {
    return {
        type: IConstant.Actions.SMARTTOUR.TOURLIST.GET_TOURLIST_REQUEST,
        pageIndex,
    };
}

function _getListSuccessAction(pageIndex, result) {
    return {
        type: IConstant.Actions.SMARTTOUR.TOURLIST.GET_TOURLIST_SUCCESS,
        pageIndex,
        result,
    };
}

function _getListFailureAction(error) {
    return {
        type: IConstant.Actions.SMARTTOUR.TOURLIST.GET_TOURLIST_FAILURE,
        message: error,
    };
}

export function openEditAction(code,isopen) {
    return {
        type: IConstant.Actions.SMARTTOUR.TOURLIST.OPEN_EDIT,
        code,
        isopen,
    };
}


