import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import UIUtil from '../../core/utils/UIUtil';
import queryString from 'query-string';
import { collectGoBackAction, addElementSelectAction } from '../../store/actions/smart-tour/elementlistAction';
import ElementSelectListView from '../../views/smart-tour/ElementSelectListView';

class CollectSelectScreen extends Component {
    constructor(props) {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.onChangeCheck = this.onChangeCheck.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render() {
        let listViewHeight = window.innerHeight - (45 +44);

        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    收藏列表
                </NavBar>
                <Content>
                    <ElementSelectListView
                        ref={(listView) => {
                            if (listView) {
                                this.listView = listView.getWrappedInstance();
                            }
                        }}
                        selectCodes={this.props.selectCodes}
                        onItemClick={this.onItemClick}
                        onChangeCheck={this.onChangeCheck}
                        tabIndex={5}
                        height={listViewHeight} />
                    <div className='collectBottom'>
                        确定
                    </div>
                </Content>
            </div>
        );
    }
    onItemClick(item) {
    }
    onChangeCheck(item) {
        let isAdd = false;
        if (this.props.selectCodes && this.props.selectCodes.indexOf(item.Code)) {
            isAdd = true;
        }
        this.props.dispatch(addElementSelectAction(item.Code, isAdd));
    }
    goBack() {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(collectGoBackAction());
    }
}

const mapStateToProps = function (state) {
    return {
        selectCodes: state.smartTour.elementSelectScreen.selectCodes
    };
};

CollectSelectScreen = connect(mapStateToProps)(CollectSelectScreen);

export default CollectSelectScreen;


