import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import TourBudgetPlaneView from '../../views/smart-tour/TourBudgetPlaneView';
import TourBudgetHotelView from '../../views/smart-tour/TourBudgetHotelView';

class TourBudgetScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render()
    {
        return (
            <div className='TourBudgetPage'>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}
                    rightContent={
                        <div style={{display:'flex',alignItems:'center',height:'100%'}}>
                            <div className="navbarRightButton" style={{color:'#ea6c86'}}>
                                &yen;3122
                            </div>
                        </div>
                    }>
                    行程预算
                </NavBar>
                <Content style={{backgroundColor:'#f6f6f6'}}>
                    {
                        this.renderPlane()
                    }
                </Content>
            </div>
        );
    }
    goBack()
    {
        this.props.history.goBack();
    }
    onItemClick(item)
    {
    }
    renderPlane(){
        return(
            <div className='tourBudgetPlaneBox'>
                <div className='tourBudgetPlaneTitle'>
                    <div className='cnName'>飞机</div>
                    <div className='enName'>Aircraft</div>
                </div>
                <div className='tourBudgetPlaneList'>
                    <TourBudgetPlaneView />
                    <TourBudgetPlaneView />
                </div>
                <div className='tourBudgetPlaneTitle'>
                    <div className='cnName'>酒店</div>
                    <div className='enName'>Hotel</div>
                </div>
                <div className='tourBudgetHotelList'>
                    <TourBudgetHotelView />
                </div>
            </div>
        );
    }
}

const mapStateToProps = function(state)
{
    return {
    };
};

TourBudgetScreen = connect(mapStateToProps)(TourBudgetScreen);

export default TourBudgetScreen;


