import { combineReducers } from 'redux';
import { Actions, SpecialStoreKey } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    // 我的游记列表
    if(!action.extraParams || action.extraParams.storeKey !== SpecialStoreKey.ME_TRAVEL_NOTE)
    {
        return {...state};
    }
    switch(action.type)
    {
    case Actions.TRAVELNOTE.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            isFetching: true,
            pageIndex: action.pageIndex,
        };
    case Actions.TRAVELNOTE.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : state.dataList;
        dataList = dataList ? dataList : [];
        dataList = dataList.concat(action.result.data);
        return {
            ...state,
            isFetching: false,
            dataList: dataList,
            total: action.result.total,
            pageIndex: action.pageIndex,
            hasMore: (action.result.total > dataList.length)
        };
    case Actions.TRAVELNOTE.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.ME.TRAVELNOTE.GO_BACK:
        return {};
    case Actions.TRAVELNOTE.LIST.DELETE:
        return {
            ...state,
            isFetching: false,
            dataList: [],
        };
    case Actions.TRAVELNOTE.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            listViewOffset: action.listViewOffset
        };
    default:
        return state;
    }
}

const listReducer = combineReducers({
    groups: groupReducer,
});

export default listReducer;


