import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    // let obj = state[action.key];
    // obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.CREATE_TOUR.SMART.GET_REQUEST:
        return {
            ...state, 
            isFetching: true,
        };
    case Actions.CREATE_TOUR.SMART.GET_SUCCESS:
        let dataList = action.result.data;
        return {
            ...state,
            isFetching: false,
            status:  action.result.status,
            dataList: dataList,
            routeName:action.result.routeName,
        };
    case Actions.CREATE_TOUR.SMART.GET_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.CREATE_TOUR.SMART.GO_BACK:
        return {};
    default:
        return state;
    }
}

const listReducer = combineReducers({
    groups: groupReducer,
});

export default listReducer;


