import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Toast, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/me.css';
import SysUtil from '../../core/utils/SysUtil';

class RegisterScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.state={
            phoneNumber:'',
            checkCode:'',
            code:'',
        };
    }

    componentDidMount()
    {
        if(this.props.isUserLogined){
            this.props.history.goBack();
        }
    }
    render()
    {
        let bg = require('../../assets/images/me/bg.jpg');
        let back = require('../../assets/images/me/back.png');
        let scrollViewHeight = window.innerHeight - 45;

        return (
            <div className="loginPage" style={{ backgroundImage: `url(${bg})` }}>
                <div className='navbar'>
                    <img src={back} alt='' className='navbarBack' onClick={()=>this.goBack()}/>
                    <span className='navbarTitle' style={{marginRight:40}}>注册</span>
                </div>
                <Content style={{ backgroundColor: 'transparent', marginTop: 0, height: scrollViewHeight }}>
                    {
                        this.renderPage()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="" />
                            :null
                    }
                </Content>
            </div>
        );
    }
    goBack()
    {
        this.props.history.goBack();
    }
    renderPage(){

        return(
            <div className='registerBox'>
                <div className='registerTip'>请输入您的手机号码</div>
                <div className='registerInputBox'>
                    <div className='registerInputItem'>
                        <div className='registerInputTitle'>手机号</div>
                        <input type='number' placeholder='请输入您的手机号码' onChange={(event)=>this.onPhoneChange(event)}/>
                    </div>
                    <div className='registerInputItem'>
                        <div className='registerInputTitle'>验证码</div>
                        <input type='number' placeholder='请输入6位数验证码' onChange={(event)=>this.onCodeChange(event)} />
                        <div className='registerSendCode' onClick={()=>this.sendCode()}>获取验证码</div>
                    </div>
                </div>
                <div className='registerButton' onClick={()=>this.nextStep()}>
                    下一步
                </div>  
            </div>
        );
    }
    scheduleTimer()
    {
        this.setState({timeCount : (this.state.timeCount - 1)});

        if (this.state.timeCount > 0)
        {
            setTimeout(this.scheduleTimer, 1000);
        }
        else
        {
            this.setState({timeCount: 60,isTimerTicking: false});
        }
    }
    sendCode() {
        let code='123123';
        this.setState({
            checkCode:code
        });
        Toast.show('验证码:'+code, 1);
    }
    onPhoneChange(event){
        this.setState({
            phoneNumber:event.target.value
        });
    }
    onCodeChange(event){
        this.setState({
            code:event.target.value
        });
    }
    nextStep(){
        if (this.state.phoneNumber.length === 0) {
            Toast.show('请输入手机号', 1);
            return;
        }
        if (!(/^1[3|4|5|7|8][0-9]{9}$/.test(this.state.phoneNumber))) {
            Toast.show('电话号码格式不正确！', 1);
            return;
        } 
        if (this.state.code.length === 0) {
            Toast.show('请输入验证码', 1);
            return;
        }
        if(this.state.code !== this.state.checkCode){
            Toast.fail('验证码不正确', 1);
            return;
        }
        this.props.history.push(`/me/register/complete/${this.state.phoneNumber}`);
    }
    componentWillUnmount(){
        this.setState({timeCount: 60, isTimerTicking: false});
    }
}
const mapStateToProps = function(state, ownProps)
{
    return{
        isUserLogined: SysUtil.isUserLogined(state),         
    };
};

RegisterScreen = connect(mapStateToProps)(RegisterScreen);

export default RegisterScreen;



