import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import { 
    asyncGetDetailAction, 
    goBackAction, 
} from '../../store/actions/strategy/detailAction';

class StrategyDetailScreen extends Component
{
    componentDidMount()
    {
        this.loadData();
    }
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let id = this.props.match.params.id;
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction(id));
                    }}>
                    详情
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view;}}>
                    {
                        this.renderHeaderView()
                    }
                    {
                        this.renderContentView()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderHeaderView()
    {
        if(!this.props.detailItem || !this.props.detailItem.data || this.props.detailItem.data.length !== 2)
        {
            return null;
        }
        return (
            <div>
                <div className="strategyDetailHeaderRow">
                    <div className="strategyDetailHeaderName">{this.props.detailItem.data[1].title}</div>
                    <div className="strategyListSecondRow">
                        <div className="strategyListItemOthers">作者：{this.props.detailItem.data[1].YHM}</div>
                        <div style={{flex: 1}}/>
                        <div className="strategyListItemOthers">时间：{this.props.detailItem.data[1].CreateDate}</div>
                    </div>
                </div>
                <div className="strategyDetailSplitLine"/>
            </div>
        );
    }
    renderContentView()
    {
        if(!this.props.detailItem || !this.props.detailItem.data || this.props.detailItem.data.length !== 2)
        {
            return null;
        }
        return (
            // <div className="strategyDetailHtml" dangerouslySetInnerHTML={{__html: this.props.detailItem.mp_content}}></div>
            <div className="strategyDetailHtml">{this.props.detailItem.data[1].Content}</div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetDetailAction(this.props.match.params.id));
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.strategy.detailScreen[ownProps.match.params.id];
    return {
        ...obj,
    };
};

StrategyDetailScreen = connect(mapStateToProps)(StrategyDetailScreen);

export default StrategyDetailScreen;


