import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action) {
    if(action.key && action.key !== 'information'){
        return state;
    }
    let obj = state[action.id];
    obj = obj ? obj : {};
    switch (action.type) {
    case Actions.INFORMATION.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state,
            [action.id]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.INFORMATION.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state,
            [action.id]: {
                ...obj,
                isFetching: false,
                detailItem: action.detailItem
            },
        };
    case Actions.INFORMATION.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state,
            [action.id]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.INFORMATION.DETAIL.GO_BACK:
        let _state = { ...state };
        delete _state[action.id];
        return {
            ..._state
        };
    case Actions.INFORMATION.DETAIL.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [action.id]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    case Actions.INFORMATION.COMMENT.GET_COMMENT_REQUEST:
        return {
            ...state,
            [action.id]: {
                ...obj,
                comment: {
                    ...state[action.id].comment,
                    isFetching: true,
                    pageIndex: action.pageIndex,
                }
            }
        };
    case Actions.INFORMATION.COMMENT.GET_COMMENT_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : state[action.id].comment.dataList;
        dataList = dataList ? dataList : [];
        dataList = dataList.concat(action.result.data);
        return {
            ...state,
            [action.id]: {
                ...obj,
                comment: {
                    ...state[action.id].comment,                    
                    isFetching: false,
                    dataList: dataList,
                    total: action.result.total,
                    pageIndex: action.pageIndex,
                    hasMore: (action.result.total > dataList.length)
                }
            },
        };
    case Actions.INFORMATION.COMMENT.GET_COMMENT_FAILURE:
        return {
            ...state,
            [action.id]: {
                ...obj,
                comment: {
                    ...state[action.id].comment,                    
                    isFetching: false,
                }
            }
        };
    case Actions.INFORMATION.COMMENT.GO_BACK:
        return {
            ...state,
            [action.id]: {
                ...obj,
                comment: {
                }
            }
        };
    case Actions.INFORMATION.COMMENT.DELETE:
        return {
            ...state,
            [action.id]: {
                ...obj,
                comment: {
                    isFetching: false,
                    dataList: [],
                }
            }
        };
    default:
        return state;
    }
}


