import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import ReactDOM from 'react-dom';
import LineListView from '../../views/smart-tour/LineListView';

class LineListScreen extends Component 
{
    constructor(props){
        super(props);
        this.onPicItemClick = this.onPicItemClick.bind(this);
        this.onBottomItemClick = this.onBottomItemClick.bind(this);        
        this.filtrate = this.filtrate.bind(this);        
    }
    render() {
        let listViewHeight = window.innerHeight - (45 + 57);

        return (
            <div className="LineList">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    rightContent={this.renderRightMenu()}
                    onLeftClick={() => {
                        this.props.history.goBack();
                    }}>
                    线路
                </NavBar>
                <Content>
                    <LineListView
                        onPicItemClick={this.onPicItemClick}
                        onBottomItemClick={this.onBottomItemClick}                        
                        height={listViewHeight}
                    />
                    <div className="LineListBottom">
                        下一步
                    </div>
                </Content>
            </div>
        );
    };

    onPicItemClick(item) {
        
    }   
    onBottomItemClick(item){
        this.props.history.push('/smart-tour/lineDetail');
    }     
    renderRightMenu() {
        return (
            <img className="cultureMapSift" onClick={this.filtrate} src={require('../../assets/images/tour/sift@2x.png')} alt='' />
        );
    }
    filtrate(){
        this.props.history.push('/smart-tour/line-filtrate');
    }
}

LineListScreen = connect()(LineListScreen);

export default LineListScreen;


