import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Toast, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import { asyncLoginAction } from '../../store/actions/me/userInfoAction';
import '../../assets/css/me.css';
import SysUtil from '../../core/utils/SysUtil';

class LoginScreen extends Component {
    constructor(props) {
        super(props);
        this.state={
            phoneNumber:'',
            pwd:'',
        };
    }

    componentDidMount()
    {
        if(this.props.isUserLogined){
            this.props.history.goBack();
        }
    }

    render() {
        let bg = require('../../assets/images/me/bg.jpg');
        let back = require('../../assets/images/me/back.png');
        let scrollViewHeight = window.innerHeight - 45;

        return (
            <div className="loginPage" style={{ backgroundImage: `url(${bg})` }}>
                <div className='navbar'>
                    <img src={back} alt='' className='navbarBack' onClick={()=>this.goBack()}/>
                    <span className='navbarTitle'>登录</span>
                    <div className='navbarRight' onClick={()=>this.showRegister()}>注册</div>
                </div>
                <Content style={{ backgroundColor: 'transparent', marginTop: 0, height: scrollViewHeight }}>
                    {
                        this.renderPage()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="登录中..." />
                            : null
                    }
                </Content>
            </div>
        );
    }
    goBack() {
        this.props.history.goBack();
    }
    showRegister(){
        this.props.history.push('/me/register');
    }
    renderPage() {
        let head = require('../../assets/images/me/head.png');
        let lock = require('../../assets/images/me/lock.png');
        let QQ_Icon = require('../../assets/images/me/QQ.png');
        let WeChat_Icon = require('../../assets/images/me/wechat.png');
        let Blog_Icon = require('../../assets/images/me/blog.png');

        return (
            <div className='loginBox'>
                <div className='loginInputBox'>
                    <div className='loginInputItem'>
                        <img src={head} alt='' />
                        <input type='number' placeholder='请输入您的手机号码' onChange={(event)=>this.onPhoneChange(event)}/>
                    </div>
                    <div className='loginInputItem'>
                        <img src={lock} alt='' />
                        <input type='password' placeholder='密码' onChange={(event)=>this.onPwdChange(event)}/>
                    </div>
                </div>
                <div className='loginButton' onClick={()=>this.doLogin()}>
                    登录
                </div>
                <div className='loginLink'>
                    <div className='loginLinkRegister' onClick={()=>this.showRegister()}>快速注册</div>
                    {/*<div className='loginLinkFind'>忘记密码</div>*/}
                </div>
                {/*<div className='loginBottom'>
                    <div className='loginDivide'>
                        <div className='loginDivideLinie'></div>
                        <div className='loginDivideTip'>第三方账户登录</div>
                        <div className='loginDivideLinie'></div>
                    </div>
                    <div className='loginOtherWay'>
                        <div className='loginOtherWayItem'>
                            <img src={WeChat_Icon} alt='' />
                        </div>
                        <div className='loginOtherWayItem'>
                            <img src={Blog_Icon} alt='' />
                        </div>
                        <div className='loginOtherWayItem'>
                            <img src={QQ_Icon} alt='' />
                        </div>
                    </div>
                </div>*/}
            </div>
        );
    }
    onPhoneChange(event){
        this.setState({
            phoneNumber:event.target.value
        });
    }
    onPwdChange(event){
        this.setState({
            pwd:event.target.value
        });
    }
    doLogin() {
        if (this.state.phoneNumber.length === 0) {
            Toast.show('请输入手机号', 1);
            return;
        }
        if (!(/^1[3|4|5|7|8][0-9]{9}$/.test(this.state.phoneNumber))) {
            Toast.show('电话号码格式不正确！', 1);
            return;
        }
        this.props.dispatch(asyncLoginAction(this.state.phoneNumber, this.state.pwd)).then(() => {
            if (this.props.status === 0) {
                Toast.success('登录成功', 1);
                this.goBack();
            } else {
                Toast.fail(this.props.data.errorMsg, 1);
            }
        });
    }
}
const mapStateToProps = function (state, ownProps) {
    let obj = state.me.userInfo;
    return {
        ...obj,
        isUserLogined: SysUtil.isUserLogined(state),                 
    };
};

LoginScreen = connect(mapStateToProps)(LoginScreen);

export default LoginScreen;



