import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';
import Content from '../../views/common/Content';
import { setScrollerPositionAction } from '../../store/actions/me/meAction';
import '../../assets/css/me.css';
import SysUtil from '../../core/utils/SysUtil';

class MeScreen extends Component
{
    render()
    {
        return (
            <div className="meScreen">
                <Content ref={(view) => { this.scrollView = view; }} heightOffset={-50}>
                    {
                        this.renderHeaderViews()
                    }
                    {
                        this.renderScoreCertView()
                    }
                    {
                        this.renderMenuGroupViews()
                    }
                </Content>
            </div>
        );
    }
    componentWillUnmount()
    {
        this.cacheScrollViewPosition();
    }
    componentDidMount()
    {
        this.restoreScrollViewPosition();
    }
    renderHeaderViews()
    {
        return (
            <div className="meHeaderBg">
                <div className="meHeaderTopBottom"/>
                <div className="meHeaderUserIcon" onClick={()=>this.ShowMyInfo()}>
                    <img src={require('../../assets/images/me/default_user_icon.png')} alt=''/>
                </div>
                <div className="meHeaderTopBottom">
                    {this.props.isUserLogined ? this.props.userInfo.username : '请登录'}
                </div>
                <div className="meHeaderMessage">
                    <img src={require('../../assets/images/me/message.png')} alt=''/>
                </div>
            </div>
        );
    }
    renderScoreCertView()
    {
        return (
            <div className="meScoreCertRow">
                <div className="meScoreCertItem">
                    <img src={require('../../assets/images/me/icon_score.png')} alt=''/>
                    我的积分
                </div>
                <div className="meScoreCertSplit"/>
                <div className="meScoreCertItem">
                    <img src={require('../../assets/images/me/icon_cert.png')} alt=''/>
                    实名认证
                </div>
            </div>
        );
    }
    renderMenuGroupViews()
    {
        let menuGroups = [
            [{
                icon: require('../../assets/images/me/icon_order.png'),
                title: '我的订单',
                isNeedLogin:true,
            }],
            [{
                icon: require('../../assets/images/me/icon_travel_notes.png'),
                title: '我的游记',
                pathname: '/me/travelNotes',
                isNeedLogin:true,
            }, {
                icon: require('../../assets/images/me/icon_journey.png'),
                title: '我的行程',
                pathname: '/smart-tour/tourlist',                
                isNeedLogin:true,
            }, {
                icon: require('../../assets/images/me/icon_activity.png'),
                title: '我的活动',
                pathname: '/me/activity',
                isNeedLogin:true,                
            }, {
                icon: require('../../assets/images/me/icon_collect.png'),
                title: '我的收藏',
                pathname: '/me/collect',
                isNeedLogin:true,
            }, {
                icon: require('../../assets/images/me/icon_qa.png'),
                title: '我的问答',
                isNeedLogin:true,
            }],
            [{
                icon: require('../../assets/images/me/icon_setting.png'),
                title: '系统设置',
                isNeedLogin:false,
            }, {
                icon: require('../../assets/images/me/icon_friends.png'),
                title: '邀请好友',
                isNeedLogin:false,                
            }, {
                icon: require('../../assets/images/me/icon_about.png'),
                title: '关于全域旅游云',
                isNeedLogin:false,                
            }]
        ];
        return menuGroups.map((group, index)=>this.renderMenuGroup(group, index));
    }
    renderMenuGroup(group, index)
    {
        return (
            <div className="meMenuGroup" key={index}>
                {
                    group.map((item, _index)=>this.renderMenuItem(item, _index, _index === group.length - 1))
                }
            </div>
        );
    }
    renderMenuItem(item, index, isLast)
    {
        return (
            <div className="meMenuRow" key={index} onClick={()=>this.showPage(item)}>
                <div className={isLast ? 'meMenuRowContent' : 'meMenuRowContent meMenuRowSplit'}>
                    <img className="meMenuRowIcon" src={item.icon} alt=''/>
                    { item.title }
                    <div style={{flex: 1}}/>
                    <img className="meMenuRowArrow" src={require('../../assets/images/arrow_right.png')} alt=''/>
                </div>
            </div>
        );
    }

    showPage(item)
    {
        if(!item || !item.pathname || item.pathname.length === 0)
        {
            return;
        }
        if(item.isNeedLogin && !this.props.isUserLogined){
            this.props.history.push('/me/login');            
        }else{
            this.props.history.push(item.pathname);            
        }
    }
    cacheScrollViewPosition()
    {
        let offset = this.scrollView.getOffsetY();
        this.props.dispatch(setScrollerPositionAction(offset));
    }
    restoreScrollViewPosition() 
    {
        if (this.scrollView) 
        {
            if (!isNaN(this.props.scrollViewOffset)) 
            {
                this.scrollView.scrollToY(this.props.scrollViewOffset);
            }
            else 
            {
                this.scrollView.scrollToY(0);
            }
        }
    }
    ShowMyInfo(){
        if(this.props.isUserLogined){

        }else{
            this.props.history.push('/me/login');
        }
    }
}

const mapStateToProps = function (state) {
    return {
        userInfo: SysUtil.getUserInfo(state),
        isUserLogined: SysUtil.isUserLogined(state), 
        ...state.me.meScreen,
    };
};

MeScreen = connect(mapStateToProps)(withRouter(MeScreen));

export default MeScreen;


