import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import ReactDOM from 'react-dom';
import TourListView from '../../views/smart-tour/TourListView';
import {goBackAction} from '../../store/actions/smart-tour/tourlistAction';

class TourListScreen extends Component 
{
    constructor(props){
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }
    render() {
        let listViewHeight = window.innerHeight - 45;

        return (
            <div className="LineList">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.props.history.goBack();
                        this.props.dispatch(goBackAction());
                    }}>
                    行程单
                </NavBar>
                <Content>
                    <TourListView
                        onItemClick={this.onItemClick}
                        height={listViewHeight}
                    />
                </Content>
            </div>
        );
    };

    onItemClick(item) {
        
    }
}

TourListScreen = connect()(TourListScreen);

export default TourListScreen;


