import React, { Component } from 'react';
import { Carousel } from 'antd-mobile';
import { withRouter } from 'react-router';
import Content from '../views/common/Content';
import '../assets/css/home.css';
import HttpClient from '../core/utils/HttpClient';

class HomeScreen extends Component 
{
    render() {
        return (
            <div className="homeScreen">
                <Content ref={(view) => { this.scrollView = view; }} heightOffset={-50}>
                    {
                        this.renderBannerViews()
                    }
                    {
                        this.renderTopMenuViews()
                    }
                    {
                        this.renderCenterMenuViews()
                    }
                </Content>
            </div>
        );
    }
    
    renderTopMenuViews()
    {
        let menus = [{
            icon: require('../assets/images/home/icon_gl.png'),
            title: '攻略',
            pathname: '/strategy/list'
        },{
            icon: require('../assets/images/home/icon_news.png'),
            title: '资讯',
            pathname: '/information/list'
        },{
            icon: require('../assets/images/home/icon_yj.png'),
            title: '游记',
            pathname: '/travelNotes/list'
        },{
            icon: require('../assets/images/home/icon_nearby.png'),
            title: '搜周边',
            pathname: '/home/mapLocation/1'
        }];
        return (
            <div className="topMenuRow">
                {
                    menus.map((item, index)=>this.renderTopMenuView(item, index))
                }
            </div>
        );
    }
    renderTopMenuView(item, index)
    {
        return (
            <div className="topMenuItem" key={index} onClick={()=>this.showPage(item)}>
                <img className="topMenuIcon" src={item.icon} alt=''/>
                {item.title}
            </div>
        );
    }
    renderCenterMenuViews()
    {
        let menuRows = [
            [{
                icon: require('../assets/images/home/icon_01.png'),
                title: '实时路况',
            }, {
                icon: require('../assets/images/home/icon_02.png'),
                title: '语音导游',
            }, {
                icon: require('../assets/images/home/icon_03.png'),
                title: '720全景',
            }, {
                icon: require('../assets/images/home/icon_04.png'),
                title: '活动',
                pathname: '/activity/list',
            }],
            [{
                icon: require('../assets/images/home/icon_05.png'),
                title: '智能行程',
                pathname: '/smart-tour/main',                
            }, {
                icon: require('../assets/images/home/icon_06.png'),
                title: '景点现状',
            }, {
                icon: require('../assets/images/home/icon_07.png'),
                title: '在线咨询',
            }, {
                icon: require('../assets/images/home/icon_08.png'),
                title: '在线投诉',
            }],
            [{
                icon: require('../assets/images/home/icon_09.png'),
                title: '现场直播',
            }, {
                icon: require('../assets/images/home/icon_10.png'),
                title: '达人伴游',
            }, {
                icon: require('../assets/images/home/icon_11.png'),
                title: '订酒店',
                pathname: '/hotel/list',   
            }, {
                icon: require('../assets/images/home/icon_12.png'),
                title: '订门票',
                pathname: '/scenic/list',   
            }],
            [{
                icon: require('../assets/images/home/icon_13.png'),
                title: '订专车',
            }, {
                icon: require('../assets/images/home/icon_14.png'),
                title: '订美食',
            }, {
                icon: require('../assets/images/home/icon_more.png'),
            }, {
                
            }],
        ];
        return (
            <div className="centerMenuRows">
                {
                    menuRows.map((row, index)=>this.renderCenterMenuRowView(row, index))
                }
            </div>
        );
    }
    renderCenterMenuRowView(row, index)
    {
        return (
            <div className="topMenuRow" key={index}>
                {
                    row.map((item, _index)=>this.renderCenterMenuItemView(item, _index, _index === row.length - 1))
                }
            </div>
        );
    }
    renderCenterMenuItemView(item, index, isLast)
    {
        return (
            <div className={isLast ? 'topMenuItem centerMenuItem' : 'topMenuItem centerMenuItem centerMenuItemBorder'} key={index} onClick={()=>this.showPage(item)}>
                {
                    item.icon
                        ? <img className="centerMenuIcon" 
                            src={item.icon} 
                            alt=''
                            style={(item.title && item.title.length > 0) ? {} : {marginBottom: '0px'}}/>
                        :
                        null
                }
                { item.title }
            </div>
        );
    }
    renderBannerViews()
    {
        let bannerList = [
            require('../assets/images/home/banner1.png'),
            require('../assets/images/home/banner2.png'),
            require('../assets/images/home/banner3.png'),
        ];
        let dotStyle = {
            height: '4px',
            width: '20px',
            borderRadius: 0,
            margin: '0 5px 10px 5px',
        };
        return (
            <Carousel
                className="homeBanners"
                autoplay={true}
                dots={true}
                dotStyle={{
                    ...dotStyle,
                    backgroundColor: '#d9d9d9',
                }}
                dotActiveStyle={{
                    ...dotStyle,
                    backgroundColor: '#fc4043',
                }}
                swiping={true}
                infinite
                selectedIndex={0}
                swipeSpeed={35}>
                {
                    bannerList.map((item, index)=><div key={index} className="homeBannerItem" style={{backgroundImage: `url(${item})`}}/>)
                }
            </Carousel>
        );
    }
    showPage(item)
    {
        if(!item || !item.pathname || item.pathname.length === 0)
        {
            return;
        }
        this.props.history.push(item.pathname);
    }
}

HomeScreen = withRouter(HomeScreen);

export default HomeScreen;


