import { Actions } from '../../../core/utils/IConstant';

export default function userInfoReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.ME.USER_INFO.LOGIN_REQUEST:
        return {
            ...state, 
            isFetching: true,
        };
    case Actions.ME.USER_INFO.LOGIN_SUCCESS:
        return {
            ...state,
            isFetching: false,
            ...action.userInfo
        };
    case Actions.ME.USER_INFO.LOGIN_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.ME.USER_INFO.EXIT_LOGIN:
        return {};
    case Actions.ME.USER_INFO.UPDATE_INFO_REQUEST:
        return {
            ...state, 
            isFetching: true,
            updateSuccess: false
        };
    case Actions.ME.USER_INFO.UPDATE_INFO_SUCCESS:
        return {
            ...state,
            isFetching: false,
            results:{
                ...action.userInfo.results
            },
            updateSuccess: true
        };
    case Actions.ME.USER_INFO.UPDATE_INFO_FAILURE:
        return {
            ...state,
            isFetching: false,
            updateSuccess: false
        };
    default:
        return state;
    }
}


