import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { ActivityIndicator, ListView, PullToRefresh } from 'antd-mobile';
import { asyncGetListAction, setScrollerPositionAction, asyncDeleteListAction } from '../../store/actions/map/listAction';
import EmptyView from '../../views/common/EmptyView';
import '../../assets/css/map.css';
import UIUtil from '../../core/utils/UIUtil';

class MapsearchListView extends Component
{
    constructor(props)
    {
        super(props);
        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        if(!this.props.dataList){
            this.loadData();
        }
    }
    componentWillReceiveProps(nextProps) {
        if (this.props.dataList !== nextProps.dataList) {
            this.setState({ dataSource: this.state.dataSource.cloneWithRows(nextProps.dataList ? nextProps.dataList : []) });
        }
    }
    render()
    {
        return (
            <div className='mapSearchListBox' style={{ height: this.props.height, backgroundColor: '#efeff4' }}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh()}/>
                        :
                        null
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{ height: this.props.height }} onClick={() => this.onRefresh()} />
                        :
                        null
                }
                {
                    this.props.isFetching
                        ?
                        <ActivityIndicator
                            toast
                            text="Loading..." />
                        : null
                }
            </div>
        );
    }
    renderListView() {
        if (!this.props.dataList || this.props.dataList.length === 0) {
            return;
        }
        return (
            <ListView
                ref={el => {
                    this.listView = el;
                    UIUtil.fixListViewHeightIssue(el, this.props.height);
                }}
                dataSource={this.state.dataSource}
                renderRow={this.renderRow}
                style={{
                    height: this.props.height,
                    overflow: 'auto',
                }}
                pullToRefresh={<PullToRefresh
                    refreshing={this.props.isFetching}
                    onRefresh={this.onRefresh}
                />}
                onEndReached={this.loadNextPage}
                renderFooter={this.renderFooter}
                pageSize={20}
                scrollRenderAheadDistance={500}
                scrollEventThrottle={50}
                onEndReachedThreshold={20} />
        );
    }
    renderRow(item, sectionID, rowID) {
        let tel=require('../../assets/images/map/icon_phone.png');
        let go=require('../../assets/images/map/icon_gothere.png');
        let detail=require('../../assets/images/map/icon_detail.png');

        return (
            <div className='mapSearchListItemBg'>
                <div className='mapSearchListItem'>
                    <div className='mapSearchListItemHead'>
                        <div className='mapSearchListItemHeadImg' style={{backgroundImage:`url(${item.GetImage.ImageFrontUrl})`}}></div>
                        <div className='mapSearchListItemHeadInfo'>
                            <div className='mapSearchListItemHeadName'>{item.CnName}</div>
                            <div className='mapSearchListItemHeadAddress'>{item.Address}</div>
                        </div>
                    </div>
                    <div className='mapSearchListItemBottom'>
                        <div className='item'>
                            <img src={tel} alt=''/>
                            <span>电话</span>
                        </div>
                        <div className='item'>
                            <img src={go} alt=''/>
                            <span>到这去</span>
                        </div>
                        <div className='item'>
                            <img src={detail} alt=''/>
                            <span>详情</span>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
    renderFooter() {
        if (!this.props.isFetching || this.props.hasMore !== true) {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    onRefresh() {
        this.props.dispatch(asyncDeleteListAction()).then(() => {
            this.loadData();
        });
    }
    loadNextPage() {
        this.loadData();
    }
    loadData(){
        if (this.props.isFetching) {
            return;
        }
        if (this.props.hasMore === false) {
            return;
        }
        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        this.props.dispatch(asyncGetListAction(pageIndex,null));
    }
}

MapsearchListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

const mapStateToProps = function(state, ownProps)
{
    return {
        ...state.map.listScreen
    };
};

MapsearchListView = connect(mapStateToProps, null, null, { withRef: true })(MapsearchListView);

export default MapsearchListView;


