Date.prototype.formatDate = function (dateFormat)    // eslint-disable-line no-extend-native
{
    let year = this.getFullYear();
    let month = this.getMonth() + 1;
    let day = this.getDate();
    let hour = this.getHours();
    let min = this.getMinutes();
    if (dateFormat === 'YYYY-MM-DD') 
    {
        return `${year}-${month}-${day}`;
    }
    else if (dateFormat === 'YYYYMMDD') 
    {
        return `${year}${month}${day}`;
    } 
    else if (dateFormat === 'HH:mm') 
    {
        return `${hour}:${min}`;
    }
};

Date.prototype.addDays = function (offset)       // eslint-disable-line no-extend-native
{
    return new Date(this.getTime() + offset * 24 * 60 * 60 * 1000);
};

export default class DateUtil 
{

    //计算2个日期之前的天数
    static GetDateDiff(startDate,endDate)  
    {  
        var startTime = new Date(Date.parse(startDate.replace(/-/g,   '/'))).getTime();     
        var endTime = new Date(Date.parse(endDate.replace(/-/g,   '/'))).getTime();     
        var dates = Math.abs((startTime - endTime))/(1000*60*60*24);     
        return  dates;    
    }

    //比较2个日期的大小
    static CompareDate(date1,date2)
    {
        var time1 = new Date(Date.parse(date1.replace(/-/g,   '/'))).getTime();     
        var time2 = new Date(Date.parse(date2.replace(/-/g,   '/'))).getTime();
        if(time1 > time2){
            return 1;
        }else if(time1 < time2){
            return -1;
        }else{
            return 0;
        }
    }
}



