import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action) {
    let arr = state.editOpenList ? state.editOpenList : [];
    if (action.isopen) {
        arr.push(action.code);
    } else {
        for (let i = 0; i < arr.length; i++) {
            if (arr[i] === action.code) {
                arr.splice(i, 1);
                break;
            }
        }
    }
    switch (action.type) {
    case Actions.SMARTTOUR.TOURLIST.GET_TOURLIST_REQUEST:
        return {
            ...state,
            isFetching: true,
            pageIndex: action.pageIndex,
        };
    case Actions.SMARTTOUR.TOURLIST.GET_TOURLIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : state.dataList;
        dataList = dataList ? dataList : [];
        dataList = dataList.concat(action.result.data);
        return {
            ...state,
            isFetching: false,
            dataList: dataList,
            total: action.result.total,
            pageIndex: action.pageIndex,
            hasMore: (action.result.total > dataList.length)
        };
    case Actions.SMARTTOUR.TOURLIST.GET_TOURLIST_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.SMARTTOUR.TOURLIST.GO_BACK:
        return {};
    case Actions.SMARTTOUR.TOURLIST.DELETE:
        return {
            ...state,
            isFetching: false,
            dataList: [],
        };
    case Actions.SMARTTOUR.TOURLIST.SET_LISTVIEW_OFFSET:
        return {
            ...state,
            listViewOffset: action.listViewOffset
        };
    case Actions.SMARTTOUR.TOURLIST.OPEN_EDIT:
        arr = [].concat(arr);
        return {
            ...state,
            editOpenList: arr
        };
    default:
        return state;
    }
}

const listReducer = combineReducers({
    groups: groupReducer,
});

export default listReducer;


