import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Router, Switch, Route } from 'react-router-dom';
import createHistory from 'history/createBrowserHistory';
import asyncScreen from './screens/common/AsyncScreen';
// import createHistory from 'history/createHashHistory';
import AppTabBar from './screens/AppTabBar';
import StrategyListScreen from './screens/strategy/StrategyListScreen';
import StrategyDetailScreen from './screens/strategy/StrategyDetailScreen';
import InformationListScreen from './screens/information/InformationListScreen';
import InformationDetailScreen from './screens/information/InformationDetailScreen';
import TravelNotesListScreen from './screens/travel-notes/TravelNotesListScreen';
import TravelNotesDetailScreen from './screens/travel-notes/TravelNotesDetailScreen';
import ActivityListScreen from './screens/activity/ActivityListScreen';
import ActivityDetailScreen from './screens/activity/ActivityDetailScreen';
import SmartTourScreen from './screens/tour/SmartTourScreen';
import CommentListScreen from './screens/comment/CommentListScreen';
import MyTravelNoteScreen from './screens/me/MyTravelNoteScreen';
import MyActivityListScreen from './screens/me/MyActivityListScreen';
import MyCollectionScreen from './screens/me/MyCollectionScreen';
import TourSettingScreen from './screens/tour/TourSettingScreen';
import CultureMapScreen from './screens/tour/CultureMapScreen';
import PreferenceScreen from './screens/tour/PreferenceScreen';
import ElementSelectScreen from './screens/tour/ElementSelectScreen';
import CollectSelectScreen from './screens/tour/CollectSelectScreen';
import TourBudgetScreen from './screens/tour/TourBudgetScreen';
import TourAdjustScreen from './screens/tour/TourAdjustScreen';
import AlarmListScreen from './screens/tour/AlarmListScreen';
import LoginScreen from './screens/me/LoginScreen';
import RegisterScreen from './screens/me/RegisterScreen';
import RegisterCompleteScreen from './screens/me/RegisterCompleteScreen';
import LineListScreen from './screens/tour/LineListScreen';
import MapSearchListScreen from './screens/map/MapSearchListScreen';
import HotelListScreen from './screens/hotel/HotelListScreen';
import HotelDetailScreen from './screens/hotel/HotelDetailScreen';
import LineFiltrateScreen from './screens/tour/LineFiltrateScreen';
import TourDetailScreen from './screens/tour/TourDetailScreen';
import TourListScreen from './screens/tour/TourListScreen';
import LineDetailScreen from './screens/tour/LineDetailScreen';
import TravelNoteEditScreen from './screens/travel-notes/TravelNoteEditScreen';
import MapLocationScreen from './screens/map/MapLocationScreen';
import FamousPersonDetailScreen from './screens/tour/FamousPersonDetailScreen';

const history = createHistory({
    basename: '/universe-travel'
});

// history.listen((location, action) => {
//     // location is an object like window.location
//     console.log(action, location.pathname, location.state);
// });

// window.addEventListener('hashchange',function(event){
//     console.log(event.oldURL + ' , ' + event.newURL);
// });
const AsyncScenicListScreen = asyncScreen(
    () => import('./screens/scenic/ScenicListScreen'),
    '景区列表'
);
const AsyncScenicDetailScreen = asyncScreen(
    () => import('./screens/scenic/ScenicDetailScreen'),
    '景区详情',
);

class App extends Component 
{
    render() 
    {
        return (
            <Router history={history}>
                <Switch>
                    <Route exact path="/" component={AppTabBar}/>
                    <Route exact path="/strategy/list" component={StrategyListScreen}/>
                    <Route exact path="/strategy/detail/:id" component={StrategyDetailScreen}/>
                    <Route exact path="/information/list" component={InformationListScreen}/>
                    <Route exact path="/information/detail/:id" component={InformationDetailScreen}/>
                    <Route exact path="/travelNotes/list" component={TravelNotesListScreen}/>
                    <Route exact path="/travelNotes/detail/:id" component={TravelNotesDetailScreen}/>
                    <Route exact path="/travelNotes/edit" component={TravelNoteEditScreen}/>
                    <Route exact path="/activity/list" component={ActivityListScreen}/>
                    <Route exact path="/activity/detail/:id" component={ActivityDetailScreen}/>
                    <Route exact path="/comment/list/:id/:type" component={CommentListScreen}/>
                    <Route exact path="/me/travelNotes" component={MyTravelNoteScreen}/>
                    <Route exact path="/me/activity" component={MyActivityListScreen}/>
                    <Route exact path="/me/collect" component={MyCollectionScreen}/>
                    <Route exact path="/smart-tour/main" component={SmartTourScreen}/>
                    <Route exact path="/smart-tour/setting" component={TourSettingScreen}/> 
                    <Route exact path="/smart-tour/culturemap" component={CultureMapScreen}/>   
                    <Route exact path="/smart-tour/preference-setting/:startTime/:endTime/:startAddress/:endAddress" component={PreferenceScreen}/> 
                    <Route exact path="/smart-tour/element-select/:startTime/:endTime/:startAddress/:endAddress" component={ElementSelectScreen}/>
                    <Route exact path="/smart-tour/collect-select" component={CollectSelectScreen}/>
                    <Route exact path="/smart-tour/tour-budget" component={TourBudgetScreen}/>
                    <Route exact path="/smart-tour/tour-adjust" component={TourAdjustScreen}/>
                    <Route exact path="/smart-tour/alarm" component={AlarmListScreen}/>
                    <Route exact path="/me/login" component={LoginScreen}/>
                    <Route exact path="/me/register" component={RegisterScreen}/>
                    <Route exact path="/me/register/complete" component={RegisterCompleteScreen}/>
                    <Route exact path="/smart-tour/linelist" component={LineListScreen}/>
                    <Route exact path="/smart-tour/lineDetail" component={LineDetailScreen}/>

                    <Route exact path="/me/register/complete/:phone" component={RegisterCompleteScreen}/>
                    <Route exact path="/map/search-list" component={MapSearchListScreen} />
                    <Route exact path="/hotel/list" component={HotelListScreen}/>
                    <Route exact path="/hotel/detail/:code" component={HotelDetailScreen}/>
                    <Route exact path="/scenic/list" component={AsyncScenicListScreen}/>
                    <Route exact path="/scenic/detail/:code" component={AsyncScenicDetailScreen}/>
                    <Route exact path="/smart-tour/line-filtrate" component={LineFiltrateScreen}/>
                    <Route exact path="/smart-tour/tourDetail" component={TourDetailScreen}/>
                    <Route exact path="/smart-tour/tourlist" component={TourListScreen}/>
                    <Route exact path="/home/mapLocation/:type" component={MapLocationScreen}/>
                    <Route exact path="/smart-tour/famousPerson" component={FamousPersonDetailScreen}/>
                    
                </Switch>
            </Router>
        );
    }
}

App = connect()(App);

export default App;



