import React, { Component } from 'react';
import { TabBar } from 'antd-mobile';

import homeIcon from '../assets/images/tab/home.png';
import mapIcon from '../assets/images/tab/map.png';
import meIcon from '../assets/images/tab/me.png';
import homeActiveIcon from '../assets/images/tab/home_active.png';
import mapActiveIcon from '../assets/images/tab/map_active.png';
import meActiveIcon from '../assets/images/tab/me_active.png';
import HomeScreen from './HomeScreen';
import MapLocationScreen from './map/MapLocationScreen';
import MeScreen from './me/MeScreen';
import {setSelectedTabIndexAction} from '../store/actions/home/homeAction';
import { connect } from 'react-redux';

class AppTabBar extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            selectedTab: 'homeTab',
        };
    }

    componentDidMount()
    {
        if(this.props.selectedTabIndex === 1){
            this.setState({
                selectedTab: 'mapTab',
            });
        }else if(this.props.selectedTabIndex === 2){
            this.setState({
                selectedTab: 'meTab',
            });
        }else{
            this.setState({
                selectedTab: 'homeTab',
            });
        }
        
    }

    render()
    {
        return (
            <TabBar
                barTintColor="white">
                <TabBar.Item
                    icon={<img src={homeIcon} alt='' className="tabBarIcon"/>}
                    selectedIcon={<img src={homeActiveIcon} alt='' className="tabBarIcon"/>}
                    selected={this.state.selectedTab === 'homeTab'}
                    onPress={() => {
                        this.setState({
                            selectedTab: 'homeTab',
                        });
                        this.props.dispatch(setSelectedTabIndexAction(0));
                    }}>
                    <HomeScreen/>
                </TabBar.Item>
                <TabBar.Item
                    icon={<img src={mapIcon} alt='' className="tabBarIcon"/>}
                    selectedIcon={<img src={mapActiveIcon} alt='' className="tabBarIcon"/>}
                    selected={this.state.selectedTab === 'mapTab'}
                    onPress={() => {
                        this.setState({
                            selectedTab: 'mapTab',
                        });
                        this.props.dispatch(setSelectedTabIndexAction(1));                        
                    }}>
                    <MapLocationScreen/>
                </TabBar.Item>
                <TabBar.Item
                    icon={<img src={meIcon} alt='' className="tabBarIcon"/>}
                    selectedIcon={<img src={meActiveIcon} alt='' className="tabBarIcon"/>}
                    selected={this.state.selectedTab === 'meTab'}
                    onPress={() => {
                        this.setState({
                            selectedTab: 'meTab',
                        });
                        this.props.dispatch(setSelectedTabIndexAction(2));                                                
                    }}>
                    <MeScreen/>
                </TabBar.Item>
            </TabBar>
        );
    }
}

const mapStateToProps = function (state) {
    return {
        ...state.home,
    };
};

AppTabBar = connect(mapStateToProps)(AppTabBar);

export default AppTabBar;



