import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function goBackAction(id) {
    return {
        type: IConstant.Actions.INFORMATION.DETAIL.GO_BACK,
        id,
    };
}

export function asyncGetDetailAction(id,userId) {
    return function (dispatch, getState) {
        dispatch(_getDetailRequestAction(id));
        return _fetchDetail(id,userId).then((resp) => {
            // resp = (resp && resp.result && resp.result.length > 0) ? resp.result[0] : undefined;
            // if (resp) {
            //     resp.mp_content = decodeURIComponent(resp.mp_content);
            // }
            dispatch(_getDetailSuccessAction(id, resp));
        }).catch((error) => {
            dispatch(_getDetailFailureAction(id, error));
        });
    };
}

function _fetchDetail(id,userId) {
    let url = HttpClient.HOST_MAIN + '/mobile/InfoContent.do';
    let kvDict = {
        NoteID: id,
        userId: userId,
    };
    return HttpClient.getData(url, kvDict).then((resp) => resp.json());
    // return new Promise((resolve, reject) => {
    //     setTimeout(() => {
    //         let resp = { 'refresh': true, 'result': [{ 'uname': '网易安顺', 'AvatarUsername': '网易安顺', 'post_Url': 'http://sns.xinxiantour.com/Forum.aspx/t-1', 'post_id': '1', 'post_postid': '1', 'title': '安顺被命名为---全国双拥模范城', 'mp_cover': 'http://sns.xinxiantour.com/Services/Forum/ForumPhoto.ashx?AttachmentID=1&PhotoType=Original', 'mp_content': '%3cp%3e%26nbsp%3b%3c%2fp%3e%0d%0a%3cp%3e%3ca%20urlLink%3d%22http%3a%2f%2fsns.xinxiantour.com%2fServices%2fForumAttachment.ashx%3fAttachmentID%3d1%22%20target%3d%22_blank%22%3e%3cimg%20style%3d%22display%3a%20block%3b%20margin-left%3a%20auto%3b%20margin-right%3a%20auto%3b%22%20src%3d%22http%3a%2f%2fsns.xinxiantour.com%2fServices%2fForumAttachment.ashx%3fAttachmentID%3d1%22%20alt%3d%221.jpg%22%20border%3d%220%22%20%2f%3e%3c%2fa%3e%3c%2fp%3e%0d%0a%3cp%20style%3d%22text-align%3a%20center%3b%22%3e%26nbsp%3b%20%e8%bf%99%e6%98%af%e6%ad%a5%e8%a1%8c%e8%a1%97%e7%9a%84%e5%85%a5%e5%8f%a3%ef%bc%8c%e6%88%aa%e6%ad%a2%e5%88%b0%e7%9b%ae%e5%89%8d%ef%bc%8c%e5%ae%83%e4%be%9d%e7%84%b6%e6%98%af%e6%96%b0%e5%8e%bf%e6%9c%80%e8%b4%9f%e7%9b%9b%e5%90%8d%ef%bc%8c%e6%9c%80%e6%9c%89%e6%96%b0%e5%8e%bf%e7%89%b9%e8%89%b2%e7%9a%84%e4%b8%80%e6%9d%a1%e8%a1%97%e3%80%82%3cbr%20%2f%3e%20%e5%bd%93%e7%84%b6%e9%9a%8f%e7%9d%80%e7%ba%a2%e6%98%9f%e8%a1%97%e7%9a%84%e5%bc%80%e5%8f%91%ef%bc%8c%e5%be%88%e6%9c%89%e5%8f%af%e8%83%bd%e8%bf%99%e9%87%8c%e7%9a%84%e4%b8%80%e5%88%87%e9%83%bd%e5%b0%86%e6%88%90%e4%b8%ba%e5%9b%9e%e5%bf%86%e3%80%82%3c%2fp%3e', 'post_time': '2015-09-15 19:52:41', 'essential': 'True', 'special': 'False', 'reply_count': '3', 'read_count': '4431' }], 'Msg': '' };
    //         resolve(resp);
    //     }, 200);
    // });
}

function _getDetailRequestAction(id) {
    return {
        type: IConstant.Actions.INFORMATION.DETAIL.GET_DETAIL_REQUEST,
        id,
    };
}

function _getDetailSuccessAction(id, resp) {
    return {
        type: IConstant.Actions.INFORMATION.DETAIL.GET_DETAIL_SUCCESS,
        id,
        detailItem: resp
    };
}

function _getDetailFailureAction(id, error) {
    return {
        type: IConstant.Actions.INFORMATION.DETAIL.GET_DETAIL_FAILURE,
        id,
        message: error
    };
}

export function setScrollerPositionAction(id,offset) {
    return {
        type: IConstant.Actions.INFORMATION.DETAIL.SET_LISTVIEW_OFFSET,
        id,
        listViewOffset: offset
    };
}


