import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncGetMeListAction,asyncDeleteListAction, setScrollerPositionAction } from '../../store/actions/activity/listAction';
import UIUtil from '../../core/utils/UIUtil';
import SysUtil from '../../core/utils/SysUtil';
import HttpClient from '../../core/utils/HttpClient';
import { SpecialStoreKey } from '../../core/utils/IConstant';
import DateUtil from '../../core/utils/DateUtil';
import EmptyView from '../../views/common/EmptyView';

class ActivityListView extends Component
{
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList)
        {
            this.loadData(this.props.type);
        }
        if(this.listView && !isNaN(this.props.listViewOffset))
        {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentWillReceiveProps(nextProps)
    {
        if(this.props.dataList !== nextProps.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(nextProps.dataList ? nextProps.dataList : [])});
        }
    }
    render()
    {
        return (
            <div className="listBox" style={{height: this.props.height}}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh()}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                        :null
                }
            </div>
        );
    }
    renderListView()
    {
        if(this.props.dataList && this.props.dataList.length > 0)
        {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );
    
            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}
                    />}
                    onEndReached={this.loadNextPage}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20}/>
            );
        }
    }
    isEmptyString(str)
    {
        return !str || str.length === 0;
    }
    onRefresh()
    {
        this.props.dispatch(asyncDeleteListAction(this.props.type,this.getExtraParams())).then(()=>{
            let pageIndex = 1;
            if(this.props.storeKey === SpecialStoreKey.ME_ACTIVITY){
                if(this.props.isUserLogined){
                    this.props.dispatch(asyncGetMeListAction(this.props.type,pageIndex,this.getExtraParams(),this.props.userInfo.uid));                    
                }
            }else{
                this.props.dispatch(asyncGetListAction(this.props.type,pageIndex,this.getExtraParams()));                
            }
        });
    }
    loadNextPage()
    {
        this.loadData();
    }
    renderFooter()
    {
        if(!this.props.isFetching || this.props.hasMore !== true)
        {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID)
    {
        let imgUrl = HttpClient.HOST_MAIN + item.MFRONTURL;
        let startDate = item.StartDate;
        let endDate = item.EndDate;
        let curDate = new Date().toLocaleDateString();
        //0：取消报名 1：已报名 2：进行中 3：活动结束 4：我要报名
        let statusStr = '------';
        let style_bg = '#666666';
        if(DateUtil.CompareDate(curDate, endDate) === 1){
            statusStr = '活动结束';
            style_bg = '#cccccc';
        }else if(DateUtil.CompareDate(curDate, startDate) === -1){
            statusStr = '未开始';
            style_bg = '#40c68b';
        }else{
            statusStr = '进行中';
            style_bg = '#fd8f37';
        }
        
        return (
            <div key={rowID} 
                className="activityListItem"
                onClick={()=>{
                    if(this.props.onItemClick)
                    {
                        this.props.onItemClick(item);
                    }
                }}>
                <div style={{backgroundImage: `url(${imgUrl})`}} className="activityListItemImg"/>
                <div className="activityListItemTextBox">
                    <div className="activityListItemLeft">
                        <div className="activityListItemTitle">{item.title}</div>
                        <div className="activityListItemTime">{item.StartDate}</div>
                    </div>
                    <div className="activityListItemRight" style={{ backgroundColor: {style_bg}}}>
                        {statusStr}
                    </div>
                </div>
            </div>
        );
    }
    loadData()
    {
        if(this.props.isFetching)
        {
            return;
        }
        if(this.props.hasMore === false)
        {
            return;
        }

        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        if(this.props.storeKey === SpecialStoreKey.ME_ACTIVITY){
            if(this.props.isUserLogined){
                this.props.dispatch(asyncGetMeListAction(this.props.type,pageIndex,this.getExtraParams(),this.props.userInfo.uid));                    
            }
        }else{
            this.props.dispatch(asyncGetListAction(this.props.type,pageIndex,this.getExtraParams()));                
        }
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(this.props.type, offsetY,this.getExtraParams()));
    }

    getExtraParams()
    {
        return {
            storeKey: this.props.storeKey,
        };
    }
}

ActivityListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

const mapStateToProps = function(state, ownProps)
{
    let storeItem = (ownProps.storeKey && ownProps.storeKey.length > 0) ? SysUtil.getStoreItemByDotConnector(state, ownProps.storeKey) : state.activity.listScreen;        
    let obj = storeItem.groups[ownProps.type];
    return {
        userInfo: SysUtil.getUserInfo(state),
        isUserLogined: SysUtil.isUserLogined(state), 
        ...obj,
    };
};

ActivityListView = connect(mapStateToProps, null, null, {withRef: true})(ActivityListView);

export default ActivityListView;


