import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Icon, NavBar } from 'antd-mobile';
import Content from '../../views/common/Content';

class AlarmListScreen extends Component {
    constructor(props) {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render() {
        let listViewHeight = window.innerHeight;
        let weather_bg = require('../../assets/images/tour/weather-banner.jpg');

        return (
            <div className="alarmListPage">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    我的提醒
                </NavBar>
                <Content style={{ height: listViewHeight, marginTop: 0 }}>
                    <div className='alarmWeather' style={{ backgroundImage: `url(${weather_bg})` }}>
                        <div className="weatherContent">
                            <div className="weathertemp">18℃</div>
                            <div>
                                <div className="weatherlowAndHeight">11°/26°</div>
                                <div className="weatherWeek">MON</div>
                            </div>
                        </div>
                    </div>
                    {
                        new Array(5).fill(0).map((item,index) => this.renderItems())
                    }
                </Content>
            </div>
        );
    };

    renderItems(){
        return(
            <div className="warnItem">
                <div className="warnItemLeft">
                    <div className="warnItemTopLine"></div>
                    <img className="warnItemIcon" src={require('../../assets/images/tour/weather-traffic.png')} alt='' />
                    <div className="warnItemBottomLine"></div>
                </div>
                <div className="warnItemRight">
                    <div  className="warnItemTitleAndTime">
                        <div  className="warnItemTitle">交通提醒</div>
                        <div  className="warnItemTime">2016-10-14 12:42</div>
                    </div>
                    <div className="warnItemContent">前方路段交通管制，目前比较拥堵，建议从其他路段绕行。</div>
                </div>
            </div>
        );
    }
    onItemClick(item) {

    }
    goBack() {
        this.props.history.goBack();
    }
}

AlarmListScreen = connect()(AlarmListScreen);

export default AlarmListScreen;


