import React, { Component } from 'react';
import {setCheckStatusChange} from '../../store/actions/preference/listAction';
import { connect } from 'react-redux';

class CheckBoxView extends Component {

    static defaultProps = {
        isSingleCheck: true,
    };

    constructor(props) {
        super(props);
        this.itemClick = this.itemClick.bind(this);
        this.state = {
            refresh: true
        };
        this.data = this.props.data;
        this.x = this.props.x;
        this.y = this.props.y;

    }
    render() {
        return (
            <div className="checkBoxView" style={{ backgroundColor: '#ffffff'}}>
                {
                    this.data.map((item, index) => (
                        <div key={index} className={item.isCheck ? 'labelCheck' : 'label'} onClick={() => this.itemClick(item, index)}>
                            {item.ZDXMC}
                        </div>
                    ))
                }
            </div>
        );
    }

    itemClick(item, index) {
        if(this.props.isSingleCheck){//单选
            this.data.map((item, index) => {
                this.data[index].isCheck = false;
                this.props.dispatch(setCheckStatusChange());
                return true;
            }
            );
        }
        this.data[index].isCheck = !this.data[index].isCheck;
        if(this.data[index].isCheck){
            this.props.dispatch(setCheckStatusChange());
        }else{
            this.props.dispatch(setCheckStatusChange());
        }
        
        this.setState(previousState => {
            return { refresh: !previousState.refresh };
        });
    }

    getCheckedItems() {
        let checkedData = [];
        this.data.map((item, index) => {
            if (item.isCheck) {
                checkedData.push(item);
            }
            return true;
        }
        );
        return checkedData;
    }
}    

CheckBoxView = connect()(CheckBoxView);

export default CheckBoxView;


