import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function asyncGetListAction(pageIndex, extraParams) {
    return function (dispatch, getState) {
        dispatch(_getListRequestAction(pageIndex, extraParams));
        return _fetchList(pageIndex).then((resp) => {
            dispatch(_getListSuccessAction(pageIndex, resp, extraParams));
        }).catch((error) => {
            dispatch(_getListFailureAction(error, extraParams));
        });
    };
}

export function goBackAction() {
    return {
        type: IConstant.Actions.SMARTTOUR.LINELIST.GO_BACK,
    };
}

export function asyncDeleteListAction(extraParams) {
    return function (dispatch, getState) {
        dispatch(deleteListAction(extraParams));
        return new Promise((resolve) => {
            resolve();
        });
    };
}

export function deleteListAction(extraParams) {
    return {
        type: IConstant.Actions.SMARTTOUR.LINELIST.DELETE,
        extraParams
    };
}

export function setScrollerPositionAction(offset, extraParams) {
    return {
        type: IConstant.Actions.SMARTTOUR.LINELIST.SET_LISTVIEW_OFFSET,
        listViewOffset: offset,
        extraParams
    };
}

function _fetchList(pageIndex) {
    // let url = HttpClient.HOST_MAIN + '/mobile/ActivityNote.do';
    // let kvDict = {
    //     curPage: pageIndex,
    //     pageSize: IConstant.PAGE_SIZE,
    // };

    // return HttpClient.getData(url, kvDict).then((resp) => resp.json());

    return new Promise((resolve, reject) => {
        setTimeout(() => {
            let list = {
                'status': '0',
                'message': '结果返回成功！',
                'total': 3,
                'data': [
                    {
                        'days': 1,
                        'imageUrl': 'http://test.hbysp.cn/Attachment/default/Source/2017/5/19/201705191820465279994.jpg',
                        'title': '贵阳一日游'
                    },
                    {
                        'days': 2,
                        'imageUrl': 'http://test.hbysp.cn/Attachment/default/Source/2017/5/19/201705191820465279994.jpg',
                        'title': '贵阳两日游'
                    },
                    {
                        'days': 3,
                        'imageUrl': 'http://test.hbysp.cn/Attachment/default/Source/2017/5/19/201705191820465279994.jpg',
                        'title': '贵阳三日游'
                    }
                ]
            };
            resolve(list);
        }, 200);
    });
}

function _getListRequestAction(pageIndex, extraParams) {
    return {
        type: IConstant.Actions.SMARTTOUR.LINELIST.GET_LINELIST_REQUEST,
        pageIndex,
        extraParams
    };
}

function _getListSuccessAction(pageIndex, result, extraParams) {
    return {
        type: IConstant.Actions.SMARTTOUR.LINELIST.GET_LINELIST_SUCCESS,
        pageIndex,
        result,
        extraParams
    };
}

function _getListFailureAction(error, extraParams) {
    return {
        type: IConstant.Actions.SMARTTOUR.LINELIST.GET_LINELIST_FAILURE,
        message: error,
        extraParams
    };
}


