import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncDeleteListAction, setScrollerPositionAction } from '../../store/actions/information/commentAction';
import UIUtil from '../../core/utils/UIUtil';
import HttpClient from '../../core/utils/HttpClient';
import EmptyView from '../../views/common/EmptyView';

class CommentListView extends Component
{
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadData = this.loadData.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.comment && this.props.comment.dataList ? this.props.comment.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList)
        {
            this.loadData(this.props.id);
        }
        if(this.listView && !isNaN(this.props.listViewOffset))
        {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentWillReceiveProps(nextProps)
    {
        if(this.props.comment && nextProps.comment && this.props.comment.dataList !== nextProps.comment.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(nextProps.comment.dataList ? nextProps.comment.dataList : [])});
        }
    }
    render()
    {
        return (
            <div className="listBox" style={{height: this.props.height}}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh()}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                        :null
                }
            </div>
        );
    }
    renderListView()
    {
        if(this.props.comment && this.props.comment.dataList && this.props.comment.dataList.length >= 0)
        {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );
    
            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.comment.isFetching}
                        onRefresh={()=>this.onRefresh()}
                    />}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    onEndReached={this.loadNextPage}                                        
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.comment.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20}/>
            );
        }
    }
    isEmptyString(str)
    {
        return !str || str.length === 0;
    }
    onRefresh()
    {
        this.props.dispatch(asyncDeleteListAction(this.props.id, this.props.type)).then(()=>{
            this.loadData();
        });
    }
    
    renderFooter()
    {
        if(!this.props.comment.isFetching || this.props.comment.hasMore !== true)
        {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID)
    {
        let imgUrl = HttpClient.HOST_MAIN + item.HEADURL;
        return (
            <div key={rowID} 
                className="informationCommentListItem"
                onClick={()=>{
                    if(this.props.onItemClick)
                    {
                        this.props.onItemClick(item);
                    }
                }}>
                <div style={{backgroundImage: `url(${imgUrl})`}} className="informationCommentListItemImg"/>
                <div className="informationCommentListItemTextBox">
                    <div className="informationCommentListItemTitle">{item.REALNAME}</div>
                    <div className="informationCommentListItemContent">{item.Content}</div>
                    <div className="informationCommentListItemTime">{item.CreateTime}</div>
                </div>
            </div>
        );
    }
    loadData()
    {
        if(this.props.comment && this.props.comment.isFetching)
        {
            return;
        }
        if(this.props.comment && this.props.comment.hasMore && this.props.comment.hasMore === false)
        {
            return;
        }
        let pageIndex = 1;
        
        // let pageIndex = this.props.comment && this.props.comment.pageIndex ? (this.props.comment.pageIndex + 1) : 1;
        this.props.dispatch(asyncGetListAction(this.props.id, pageIndex, this.props.type));
    }

    loadNextPage()
    {
        if(this.props.comment && this.props.comment.isFetching)
        {
            return;
        }
        let pageIndex = this.props.comment.pageIndex + 1;
        this.props.dispatch(asyncGetListAction(this.props.id, pageIndex, this.props.type));
    }

    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(this.props.id, offsetY, this.props.type));
    }
}

CommentListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

const mapStateToProps = function(state, ownProps)
{
    let obj = state.information.detailScreen[ownProps.id];
    if(ownProps.type === 'imformation'){
        obj = state.information.detailScreen[ownProps.id];
    }else if(ownProps.type === 'travelNotes'){
        obj = state.travelNotes.detailScreen[ownProps.id];
    }
    return {
        ...obj,
    };
};

CommentListView = connect(mapStateToProps, null, null, {withRef: true})(CommentListView);

export default CommentListView;


