import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { ListView, ActivityIndicator } from 'antd-mobile';
import UIUtil from '../../core/utils/UIUtil';

class TourDayListView extends Component {
    constructor(props) {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.renderFooter = this.renderFooter.bind(this);

        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount() {
        // 处理返回
        // if (!this.props.dataList) {
        //     this.loadData();
        // }
        if (this.listView && !isNaN(this.props.listViewOffset)) {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentWillReceiveProps(nextProps) {
        if (this.props.dataList !== nextProps.dataList) {
            this.setState({ dataSource: this.state.dataSource.cloneWithRows(nextProps.dataList ? nextProps.dataList : []) });
        }
    }
    render() {
        return (
            <div className="listBox" style={{ height: this.props.height }}>
                {
                    this.renderListView()
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator
                            toast
                            text="Loading..." />
                        : null
                }
            </div>
        );
    }
    renderListView() {
        if (this.props.dataList && this.props.dataList.length > 0) {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );

            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50} />
            );
        }
    }
    isEmptyString(str) {
        return !str || str.length === 0;
    }
    renderFooter() {
        if (!this.props.isFetching || this.props.hasMore !== true) {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID) {
        let icon = require('../../assets/images/tour/line-scenic.png');
        if(item.type === 'JQ'){
            icon = require('../../assets/images/tour/line-scenic.png');
        }else{
            icon = require('../../assets/images/tour/line-hourse.png');
        }
        return (
            <div className="tourdayitem" >
                <div className="tourdayitemleft" >
                    <div className="tourdayitemlineTop"></div>
                    <img className="tourdayitemicon" src={icon} alt='' />
                    <div className="tourdayitemlineBottom"></div>
                </div>
                <div className="tourdayitemright" >
                    <div className="tourdayitemadress" >{item.name}</div>
                    <div className="tourdayitemarrivertime" >{item.startEffectiveTime}</div>
                    <div className="tourdayiteminfo">
                        <div className="tourdayitemway_time_icon">
                            <div className="tourdayitemway_time">
                                <div className="tourdayitemway" >营业时间</div>
                                <div className="tourdayitemtime" >{item.openTime}</div>
                            </div>
                            <img className="tourdayitemarrowicon" src={require('../../assets/images/tour/left_go.png')} alt='' />
                        </div>
                        <div className="tourdayitemway_linediv">
                            <div className="tourdayitemway_line" ></div>
                        </div>
                        <div className="tourdayitemstart_end">{item.address}</div>
                        <div className="tourdayitemcontent">{item.intro}</div>
                    </div>
                </div>
            </div>
        );
    }

    cacheScrollOffset() {
        // let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        // this.props.dispatch(setScrollerPositionAction(offsetY, this.getExtraParams()));
    }
}

TourDayListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

// const mapStateToProps = function (state, ownProps) {
//     let storeItem = (ownProps.storeKey && ownProps.storeKey.length > 0) ? SysUtil.getStoreItemByDotConnector(state, ownProps.storeKey) : state.smartTour.recommendlistScreen;
//     let obj = storeItem.groups;
//     return {
//         ...obj,
//     };
// };

TourDayListView = connect(null ,  null, null, { withRef: true })(TourDayListView);

export default TourDayListView;


