import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncDeleteListAction, setScrollerPositionAction } from '../../store/actions/smart-tour/elementlistAction';
import UIUtil from '../../core/utils/UIUtil';
import EmptyView from '../../views/common/EmptyView';

// 景点、餐饮、购物、娱乐、酒店
const sortFields: Array = ['scenic', 'food', 'shop', 'fun', 'hotel', 'collect'];
const fieldCodes: Array = ['scenicCodes', 'foodCodes', 'shopCodes', 'funCodes', 'hotelCodes'];

class ElementSelectListView extends Component {
    constructor(props) {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);

        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount() {
        // 处理返回
        if (!this.props.dataList) {
            this.loadData();
        }
        if (this.listView && !isNaN(this.props.listViewOffset)) {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentWillReceiveProps(nextProps) {
        if (this.props.dataList !== nextProps.dataList) {
            this.setState({ dataSource: this.state.dataSource.cloneWithRows(nextProps.dataList ? nextProps.dataList : []) });
        }
    }
    render() {
        return (
            <div className="scenicListBox" style={{ height: this.props.height }}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{ height: this.props.height }} onClick={() => this.onRefresh(this.props.inputKeyword)} />
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator
                            toast
                            text="Loading..." />
                        : null
                }
            </div>
        );
    }
    renderListView() {
        if (this.props.dataList && this.props.dataList.length > 0) {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );

            return (
                <ListView
                    ref={(el) => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={() => this.onRefresh(this.props.inputKeyword)}
                    />}
                    onEndReached={this.loadNextPage}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20} />
            );
        }
    }
    isEmptyString(str) {
        return !str || str.length === 0;
    }
    onRefresh() {
        this.props.dispatch(asyncDeleteListAction(sortFields[this.props.tabIndex], this.getExtraParams())).then(() => {
            this.loadData();
        });
        // this.props.dispatch(deleteListAction(sortFields[this.props.tabIndex], sortTypes[this.props.tabIndex]));
        // setTimeout(()=>{
        //     this.loadData(keyword);
        // }, 50);
    }
    loadNextPage() {
        this.loadData();
    }
    renderFooter() {
        if (!this.props.isFetching || this.props.hasMore !== true) {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID) {
        let checked = require('../../assets/images/tour/add_checked.png');
        let imgUrl = item.picture ? item.picture : '';
        let name = item.name;
        let address = item.address;
        let intro = item.intro;
        let id = item.id;
       
        return (
            <div key={rowID}
                className="elementListItem"
                onClick={() => {
                    if (this.props.onItemClick) {
                        this.props.onItemClick(item);
                    }
                }}>
                <div className='elementListItemImgBox' onClick={() => this.props.onChangeCheck(item)}>
                    <div style={{ backgroundImage: `url(${imgUrl})` }} className="scenicListItemImg" />
                    {

                        this.props.selectCodes[fieldCodes[this.props.selectedTabIndex]] && this.props.selectCodes[fieldCodes[this.props.selectedTabIndex]].indexOf(id) > -1
                            ?
                            <div className='elementListItemImgMask'>
                                <img src={checked} alt='' />
                            </div>
                            :
                            null
                    }
                </div>
                <div className="elementListItemTextBox">
                    <div className="elementListItemName">{name}</div>
                    {/* <div className="elementListItemStatus" style={{color:'#0fbebf'}}>首选</div> */}
                    <div className="elementListItemOthers">{address}</div>
                    <div className="elementListItemOthers">{intro}</div>
                </div>
            </div>
        );
    }
    loadData() {
        if (this.props.isFetching) {
            return;
        }
        if (this.props.hasMore === false) {
            return;
        }

        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        this.props.dispatch(asyncGetListAction(pageIndex, sortFields[this.props.tabIndex], this.getExtraParams()));
    }
    cacheScrollOffset() {
        if (!this.listView) {
            return;
        }
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(sortFields[this.props.tabIndex], offsetY, this.getExtraParams()));
    }
    getExtraParams() {
        return {
            storeKey: this.props.storeKey,
        };
    }
}

ElementSelectListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
    onChangeCheck: PropTypes.func,
};

const mapStateToProps = function (state, ownProps) {
    let storeItem = state.smartTour.elementSelectScreen;
    let obj = storeItem.groups[sortFields[ownProps.tabIndex]];
    return {
        selectedTabIndex: storeItem.selectedTabIndex,
        ...obj,
    };
};

ElementSelectListView = connect(mapStateToProps, null, null, { withRef: true })(ElementSelectListView);

export default ElementSelectListView;


