import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function selectedTabIndexReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.CULTUREMAP.LIST.SET_SELECTED_TAB_INDEX:
        return action.index;
    case Actions.CULTUREMAP.LIST.GO_BACK:
        return 0;
    default:
        return state;
    }
}

function groupReducer(state = {}, action)
{
    let obj = state[action.key];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.CULTUREMAP.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [action.key]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.CULTUREMAP.LIST.GET_LIST_SUCCESS:
        let dataList = action.result.results;
        return {
            ...state,
            [action.key]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
            }
        };
    case Actions.CULTUREMAP.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [action.key]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.CULTUREMAP.LIST.GO_BACK:
        return {};
    default:
        return state;
    }
}

const listReducer = combineReducers({
    selectedTabIndex: selectedTabIndexReducer,
    groups: groupReducer,
});

export default listReducer;


