import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { NavBar, SearchBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import { APP_CITY_LATITUDE, APP_CITY_LONGITUDE } from '../../core/utils/IConstant';
import '../../assets/css/map.css';

class MapLocationScreen extends Component
{
    Menu_Data=[
        {
            icon:'',
            title:'公交站'
        },
        {
            icon:'',
            title:'便利店'
        },
        {
            icon:'',
            title:'加油站'
        },
        {
            icon:'',
            title:'卫生间'
        },
        {
            icon:'',
            title:'银行'
        },
        {
            icon:'',
            title:'医院'
        }
    ];
    constructor(props)
    {
        super(props);
        this.goBack = this.goBack.bind(this);        
        this.state = {
            mapHeight: 0,
        };
    }
    componentDidMount()
    {
        this.updateWindowDimensions();
    }

    componentWillReceiveProps(nextProps)
    {
        if(!this.props.baiduMapInited && nextProps.baiduMapInited)
        {
            this.initMap();
        }
    }

    render()
    {
        let scrollViewHeight=window.innerHeight - 50;
        if(this.props.match.params.type === '1'){
            scrollViewHeight = window.innerHeight;
        }
        return (
            <div className='mapLocationPage'>
                <NavBar mode="dark" 
                    icon={this.props.match.params.type === '1' ? <Icon type="left" />:null}
                    onLeftClick={this.goBack}
                    className="navBar">
                    安顺
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view;}} style={{height: scrollViewHeight,marginTop:0}}>
                    <div id="elementMarkerMap" style={{height: (this.state.mapHeight + 'px')}}></div>
                    {
                        this.renderSearch()
                    }
                    {
                        this.renderMenu()
                    }
                </Content>
            </div>
        );
    }
    
    goBack() {
        this.props.history.goBack();
    }

    initMap()
    {
        // 地图标记图片
        // this.markerIcon = new window.BMap.Icon(require('../../assets/images/map/common_poi@2x.png'), new window.BMap.Size(30, 30), {
        //     anchor: new window.BMap.Size(15, 30),
        //     imageSize: new window.BMap.Size(30, 30)
        // });

        this.map = this.map ? this.map : new window.BMap.Map('elementMarkerMap');
        this.map.centerAndZoom(new window.BMap.Point(APP_CITY_LONGITUDE, APP_CITY_LATITUDE), 16);
        this.map.addControl(new window.BMap.NavigationControl({type: window.BMAP_NAVIGATION_CONTROL_SMALL, anchor: window.BMAP_ANCHOR_BOTTOM_RIGHT, enableGeolocation: true, offset: new window.BMap.Size(20, 20)}));
        this.map.addControl(new window.BMap.GeolocationControl({enableAutoLocation: false}));
    }
    renderSearch(){
        let menu=require('../../assets/images/menu_gray.png');

        return(
            <div className='mapSearchBox'>
                <SearchBar placeholder="请输入关键字"
                    className='mapSearchBar'
                    maxLength={20}
                    onFocus={() => { this.searchBarFocus(); }}
                />
                <img src={menu} alt='' className='mapSearchMenu' onClick={()=>this.showSearchList()}/>
            </div>
        );
    }
    searchBarFocus(){

    }
    showSearchList(){
        this.props.history.push('/map/search-list');
    }
    renderMenu(){
        let icon=require('../../assets/images/map/bus.png');

        return(
            <div className='mapMenuBox'>
                {
                    this.Menu_Data.map((item, index) => <div className='mapMenuItem' key={index}>
                        <img src={icon} alt='' className='mapMenuItemIcon' />
                        <div className='mapMenuItemTitle'>{item.title}</div>
                    </div>)
                }
            </div>
        );
    }
    
    updateWindowDimensions()
    {
        let height = window.innerHeight - 50;
        if(this.props.match.params.type === '1'){
            height = window.innerHeight;
        }
        this.setState({ mapHeight: height }, ()=>{
            if (this.props.baiduMapInited)
            {
                this.initMap();
            }
        });
    }
}

const mapStateToProps = function(state, ownProps)
{
    return {
        baiduMapInited: state.system.baiduMapInited,
    };
};

MapLocationScreen = connect(mapStateToProps)(withRouter(MapLocationScreen));

export default MapLocationScreen;


