import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.STRATEGY.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state,
            [action.id]: {
                isFetching: true,
            }
        };
    case Actions.STRATEGY.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state,
            [action.id]: {
                isFetching: false,
                detailItem: action.detailItem
            },
        };
    case Actions.STRATEGY.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state,
            [action.id]: {
                isFetching: false,
            }
        };
    case Actions.STRATEGY.DETAIL.GO_BACK:
        let _state = {...state};
        delete _state[action.id];
        return {
            ..._state
        };
    default:
        return state;
    }
}


