import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import UIUtil from '../../core/utils/UIUtil';
import TourAdjustListView from '../../views/smart-tour/TourAdjustListView';

class TourAdjustScreen extends Component
{
    tabs = [
        {title: '酒店', tabIndex: 0},
        {title: '景区', tabIndex: 1}
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.showAddList=this.showAddList.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render()
    {
        let tabHeight = window.innerHeight - 45;
        let listViewHeight = window.innerHeight - (45 + 44);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}
                    rightContent={
                        <div style={{display:'flex',alignItems:'center',height:'100%'}}>
                            <div className="navbarRightButton">
                                完成
                            </div>
                        </div>
                    }>
                    调整行程
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        style={{height: tabHeight}}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        swipeable={false}
                        onChange={(tab, index) => { 
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><TourAdjustListView key={index} 
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                showAddList={this.showAddList}
                                tabIndex={item.tabIndex} 
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
    }
    goBack()
    {
        this.props.history.goBack();
    }
    showAddList(){
        this.props.history.push('/smart-tour/collect-select');
    }
}

const mapStateToProps = function(state)
{
    return {
    };
};

TourAdjustScreen = connect(mapStateToProps)(TourAdjustScreen);

export default TourAdjustScreen;


