import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import ReactDOM from 'react-dom';
import LineListView from '../../views/smart-tour/LineListView';
import CheckBoxView from '../../views/tour/CheckBoxView';

class LineFiltrateScreen extends Component {
    constructor(props) {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.clear = this.clear.bind(this);
        this.submit = this.submit.bind(this);
    }
    render() {

        return (
            <div className="LineFiltrate">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="cross" />}
                    onLeftClick={() => {
                        this.props.history.goBack();
                    }}>
                    线路筛选
                </NavBar>
                <Content>
                    {
                        this.renderFiltrate()
                    }
                    <div className="bottom">
                        <div className="bottomBtn1" onClick={this.clear}>清除选项</div>
                        <div className="bottomBtn2" onClick={this.submit}>确定</div>
                    </div>
                </Content>
            </div>
        );
    };

    renderFiltrate() {
        let data1 = [
            { value: 0, label: '全部', isCheck: false },
            { value: 1, label: '贵阳市', isCheck: false },
            { value: 2, label: '花溪区', isCheck: false },
        ];
        let data2 = [
            { value: 0, label: '不限', isCheck: false },
            { value: 1, label: '1-2天', isCheck: false },
            { value: 2, label: '3-5天', isCheck: false },
        ];
        return (
            <div>
                <div className="title">线路所在区域(可多选)</div>
                <CheckBoxView
                    ref={(view) => { this.checkBoxView1 = view; }}
                    isSingleCheck={false}
                    data={data1}
                />
                <div className="title">天数</div>
                <CheckBoxView
                    ref={(view) => { this.checkBoxView2 = view; }}
                    isSingleCheck={true}
                    data={data2}
                />
            </div>
        );
    }

    clear(){

    }

    submit(){
        this.props.history.goBack();        
    }

    onItemClick(item) {

    }
}

LineFiltrateScreen = connect()(LineFiltrateScreen);

export default LineFiltrateScreen;


