import { combineReducers } from 'redux';
import homeReducer from './home';
import mineReducer from './me';
import strategyReducer from './strategy';
import informationReducer from './information';
import travelNotesReducer from './travel-notes';
import activityReducer from './activity';
import systemReducer from './systemReducer';
import smartTourReducer from './smart-tour';
import mapReducer from './map';
import hotelReducer from './hotel';
import scenicReduer from './scenic';
import cultureMapReducer from './culture-map';
import preferenceReducer from './preference';
import createTourReducer from './create-tour';

const appReducers = combineReducers({
    home: homeReducer,
    me: mineReducer,
    strategy: strategyReducer,
    information:informationReducer,
    travelNotes: travelNotesReducer,
    activity: activityReducer,
    smartTour: smartTourReducer,
    system: systemReducer,
    map:mapReducer,
    hotel:hotelReducer,
    scenic:scenicReduer,
    cultureMap:cultureMapReducer,
    preference:preferenceReducer,
    createTour:createTourReducer,
});

export default appReducers;


