import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Button, Toast, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/me.css';
import HttpClient from '../../core/utils/HttpClient';
import { asyncLoginAction } from '../../store/actions/me/userInfoAction';

class RegisterCompleteScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.state={
            pwd:'',
            confirmPwd:'',
        };
    }

    render()
    {
        let bg = require('../../assets/images/me/bg.jpg');
        let back = require('../../assets/images/me/back.png');
        let scrollViewHeight = window.innerHeight - 45;

        return (
            <div className="loginPage" style={{ backgroundImage: `url(${bg})` }}>
                <div className='navbar'>
                    <img src={back} alt='' className='navbarBack' onClick={()=>this.goBack()} />
                    <span className='navbarTitle'>注册</span>
                </div>
                <Content style={{ backgroundColor: 'transparent', marginTop: 0, height: scrollViewHeight }}>
                    {
                        this.renderPage()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="" />
                            :null
                    }
                </Content>
            </div>
        );
    }
    goBack()
    {
        this.props.history.goBack();
    }
    renderPage(){
        let button_bg=require('../../assets/images/me/complete.png');

        return(
            <div className='registerBox'>
                <div className='registerTip'>请设置密码</div>
                <div className='registerInputBox'>
                    <div className='registerInputItem'>
                        <div className='registerInputTitle'>密码</div>
                        <input type='password' placeholder='6-16位的数字或字母' onChange={(event)=>this.onPwdChange(event)}/>
                    </div>
                    <div className='registerInputItem'>
                        <div className='registerInputTitle'>确认密码</div>
                        <input type='password' placeholder='6-16位的数字或字母' onChange={(event)=>this.onConfirmPwdChange(event)}/>
                    </div>
                </div>
                <div className='registerButton' onClick={()=>this.onRegisterComplete()}>
                    完成
                </div>  
            </div>
        );
    }
    onPwdChange(event){
        this.setState({
            pwd:event.target.value
        });
    }
    onConfirmPwdChange(event){
        this.setState({
            confirmPwd:event.target.value
        });
    }
    onRegisterComplete() {
        if (this.state.pwd.length === 0) {
            Toast.show('密码不能为空', 1);
            return;
        }
        // if (!/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/.test(this.state.pwd)) {
        //     Toast.show('密码格式不正确', 1);
        //     return;
        // }
        if (this.state.pwd.length < 6 || this.state.pwd.length > 16) {
            Toast.show('密码位数不正确', 1);
            return;
        }
        if (this.state.pwd !== this.state.confirmPwd) {
            Toast.fail('两次输入的密码不一致', 1);
            return;
        }

        let url = HttpClient.HOST_MAIN + '/mobile/registUser.do';
        let kvDict = {
            userName: this.props.match.params.phone,
            reloginpass: this.state.confirmPwd,
            password: this.state.pwd
        };

        HttpClient.getData(url, kvDict)
            .then((resp) => resp.json()).then((resp) => {
                if (resp.status === 0) {
                    Toast.show('注册成功');
                    this.props.dispatch(asyncLoginAction(this.props.match.params.phone, this.state.pwd)).then(() => {
                        if (this.props.status === 0) {
                            // Toast.show('登录成功');
                            this.props.history.goBack();
                        } else {
                            Toast.fail(this.props.data.errorMsg, 1);
                        }
                    });
                }else{
                    Toast.fail(resp.data.errorMessage);
                }
            });
    }
}
const mapStateToProps = function(state, ownProps)
{
    let obj = state.me.userInfo;
    return {
        ...obj,
    };
};

RegisterCompleteScreen = connect(mapStateToProps)(RegisterCompleteScreen);

export default RegisterCompleteScreen;



