import { combineReducers } from 'redux';
import recommendlistReducer from './recommendlistReducer';
import elementlistReducer from './elementlistReducer';
import tourlistReducer from './tourlistReducer';

const smartTourReducer = combineReducers({
    recommendlistScreen: recommendlistReducer,
    elementSelectScreen:elementlistReducer,
    tourlistScreen: tourlistReducer,
});

export default smartTourReducer;


