import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { ListView, SwipeAction } from 'antd-mobile';
import UIUtil from '../../core/utils/UIUtil';

class TourAdjustListView extends Component {
    dataList = [
        {
            'Code': 'JD000195',
            'CnName': '万绿城铂瑞兹酒店',
            'Address': '安顺市西秀区黄果树大街万绿城国际广场（靠近安顺大润发）',
            'MinSalePrice': 418.00,
            'HotelType': '奢侈版豪华酒店',
            'GetImage': {
                'ImageFrontUrl': 'http://103.10.86.241:8081/Attachment/default/Small/2015/7/8/DSC04708201507081526328073234.JPG',
                'ImageList': null
            }
        },
        {
            'Code': 'JD000299',
            'CnName': '安顺百灵希尔顿逸林酒店',
            'Address': '安顺西秀区虹山湖路42号',
            'MinSalePrice': 619.00,
            'HotelType': '豪华酒店',
            'GetImage': {
                'ImageFrontUrl': 'http://103.10.86.241:8081/Attachment/default/Small/2017/8/2/201708021957359602545.jpg',
                'ImageList': null
            }
        },
        {
            'Code': 'JD000181',
            'CnName': '黄果树合源大酒店',
            'Address': '安顺黄果树景区售票处近黄果树客运站',
            'MinSalePrice': 268.00,
            'HotelType': '豪华酒店',
            'GetImage': {
                'ImageFrontUrl': 'http://103.10.86.241:8081/Attachment/default/Small/2015/6/9/20150609183353000665396197.jpg',
                'ImageList': null
            }
        },
    ];
    constructor(props) {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onDel=this.onDel.bind(this);

        this.dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: this.dataSource.cloneWithRows(this.dataList ? this.dataList : []),
        };
    }
    componentDidMount() {
        // 处理返回
        // if(!this.props.dataList)
        // {
        //     this.loadData();
        // }
        // if(this.listView && !isNaN(this.props.listViewOffset))
        // {
        //     this.listView.scrollTo(0, this.props.listViewOffset);
        // }
    }
    componentWillReceiveProps(nextProps) {
        // if(this.props.dataList !== nextProps.dataList)
        // {
        //     this.setState({dataSource: this.state.dataSource.cloneWithRows(nextProps.dataList ? nextProps.dataList : [])});
        // }
    }
    render() {
        return (
            <div className="adjustListBox" style={{ height: this.props.height }}>
                {
                    this.renderControl()
                }
                {
                    this.renderListView()
                }
                {/*{
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh(this.props.inputKeyword)}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                        :null
                }*/}
            </div>
        );
    }
    renderControl() {
        return (
            <div className='adjustControl' onClick={()=>this.props.showAddList()}>
                {
                    this.props.tabIndex === 0
                        ?
                        '添加酒店'
                        :
                        '添加景区'
                }
            </div>
        );
    }
    renderListView() {
        if (this.dataList && this.dataList.length > 0) {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );

            return (
                <ListView
                    ref={(el) => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height - 44);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height - 44,
                        overflow: 'auto',
                    }}
                    onEndReached={this.loadNextPage}
                    initialListSize={this.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20} />
            );
        }
    }
    isEmptyString(str) {
        return !str || str.length === 0;
    }
    renderRow(item, sectionID, rowID) {

        return (
            <SwipeAction
                key={rowID}
                style={{ backgroundColor: 'gray' }}
                autoClose
                right={[
                    {
                        text: '删除',
                        onPress: () => this.onDel(item),
                        style: { backgroundColor: '#F4333C', color: 'white' },
                    },
                ]}
            >
                <div
                    className="adjustListItem"
                    onClick={() => {
                        if (this.props.onItemClick) {
                            this.props.onItemClick(item);
                        }
                    }}>
                    <div className='adjustListItemImg' style={{ backgroundImage: `url(${item.GetImage.ImageFrontUrl})` }}></div>
                    <div className='adjustListItemContent'>
                        <div className='adjustListItemRow'>
                            {item.CnName}
                        </div>
                        <div className='adjustListItemOtherRow'>
                            {item.HotelType} <span className='adjustListItemSalePrice'>&yen;{item.MinSalePrice}</span>起
                        </div>
                        <div className='adjustListItemOtherRow'>{item.Address}</div>
                    </div>
                </div>
            </SwipeAction>
        );
    }
    onDel(item){
        for(let i=0;i<this.dataList.length;i++){
            if(this.dataList[i].Code===item.Code){
                this.dataList.splice(i,1);
                break;
            }
        }
        this.setState({
            dataSource: this.dataSource.cloneWithRows(this.dataList ? this.dataList : []),
        });
    }
}

TourAdjustListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
    showAddList:PropTypes.func,
};

const mapStateToProps = function (state, ownProps) {
    return {
    };
};

TourAdjustListView = connect(mapStateToProps, null, null, { withRef: true })(TourAdjustListView);

export default TourAdjustListView;


