import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import UIUtil from '../../core/utils/UIUtil';
import queryString from 'query-string';
import { setSelectedTabIndexAction, goBackAction, addElementSelectAction } from '../../store/actions/smart-tour/elementlistAction';
import ElementSelectListView from '../../views/smart-tour/ElementSelectListView';
import { asyncSelfCreateTourAction } from '../../store/actions/create-tour/listAction';
import SysUtil from '../../core/utils/SysUtil';

// 景点、餐饮、购物、娱乐、酒店
const fieldCodes: Array = ['scenicCodes', 'foodCodes', 'shopCodes', 'funCodes', 'hotelCodes'];
class ElementSelectScreen extends Component {
    tabs = [
        { title: '景点', tabIndex: 0 },
        { title: '餐饮', tabIndex: 1 },
        { title: '购物', tabIndex: 2 },
        { title: '娱乐', tabIndex: 3 },
        { title: '酒店', tabIndex: 4 },
    ];
    constructor(props) {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.onChangeCheck = this.onChangeCheck.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render() {
        let tabHeight = window.innerHeight - (45 + 44);
        let listViewHeight = window.innerHeight - (45 + 44 + 44);
        let add_star = require('../../assets/images/tour/add_star.png');
        let filter = require('../../assets/images/tour/filter.png');
        let loc = require('../../assets/images/tour/add_loc.png');

        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}
                    rightContent={
                        <div style={{ display: 'flex', alignItems: 'center', height: '100%', display: 'none' }}>
                            <div className="navbarRightButton">
                                <img className="navbarRightButtonImage" src={filter} alt='' />
                            </div>
                            <div className="navbarRightButton">
                                <img className="navbarRightButtonImage" src={loc} alt='' />
                            </div>
                        </div>
                    }>
                    旅游
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        ref={(ref) => UIUtil.updateComponentHeight(ref, tabHeight)}
                        style={{ height: tabHeight }}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => {
                            this.props.dispatch(setSelectedTabIndexAction(index));
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index) => <ElementSelectListView key={index}
                                ref={(listView) => {
                                    if (listView) {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                selectCodes={this.props.selectCodes}
                                onItemClick={this.onItemClick}
                                onChangeCheck={this.onChangeCheck}
                                tabIndex={item.tabIndex}
                                height={listViewHeight} />)
                        }
                    </Tabs>
                    <div className='elementSelectBottom'>
                        <div className='elementSelectBottomItem prevstep' onClick={this.goBack}>上一步</div>
                        <div className='elementSelectBottomItem' onClick={() => this.showCollectList()}>
                            <img src={add_star} alt='' className='starIcon' />
                            <span className='selectNum'>
                                {
                                    this.props.selectCodes
                                        && this.props.selectCodes.scenicCodes
                                        && this.props.selectCodes.foodCodes
                                        && this.props.selectCodes.shopCodes
                                        && this.props.selectCodes.funCodes
                                        && this.props.selectCodes.hotelCodes
                                        ?
                                        (this.props.selectCodes.scenicCodes.length +
                                            this.props.selectCodes.foodCodes.length +
                                            this.props.selectCodes.shopCodes.length +
                                            this.props.selectCodes.funCodes.length +
                                            this.props.selectCodes.hotelCodes.length)
                                        :
                                        0
                                }
                            </span>
                        </div>
                        <div className='elementSelectBottomItem nextstep' onClick={() => this.showNext()}>下一步</div>
                    </div>
                </Content>
            </div>
        );
    }
    onItemClick(item) {
    }
    onChangeCheck(item) {
        let id = '';
        let isAdd = false;
        if (this.props.selectedTabIndex === 0) {
            id = item.id;
        } else if (this.props.selectedTabIndex === 1) {
            id = item.id;
        } else if (this.props.selectedTabIndex === 2) {
            id = item.id;
        } else if (this.props.selectedTabIndex === 3) {
            id = item.id;
        } else if (this.props.selectedTabIndex === 4) {
            id = item.id;
        }

        if (!this.props.selectCodes[fieldCodes[this.props.selectedTabIndex]]) {
            isAdd = true;
        }
        if (this.props.selectCodes[fieldCodes[this.props.selectedTabIndex]] && this.props.selectCodes[fieldCodes[this.props.selectedTabIndex]].indexOf(id) === -1) {
            isAdd = true;
        }
        this.props.dispatch(addElementSelectAction(id, this.props.selectedTabIndex, isAdd));
    }
    goBack() {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }
    showCollectList() {
        this.props.history.push('/smart-tour/collect-select');
    }
    showNext() {
        let startTime = this.props.match.params.startTime;
        let endTime = this.props.match.params.endTime;
        let startAddress = this.props.match.params.startAddress;
        let endAddress = this.props.match.params.endAddress;
        
        if (this.props.selectCodes.scenicCodes &&
            (this.props.selectCodes.scenicCodes.length
                + this.props.selectCodes.foodCodes.length
                + this.props.selectCodes.shopCodes.length
                + this.props.selectCodes.funCodes.length
                + this.props.selectCodes.hotelCodes.length) !== 0) {

            if (this.props.isUserLogined) {
                let param = {
                    'userId' : this.props.userInfo.uid,
                    'departureLocality': startAddress,
                    'destination': endAddress,
                    'startTime': startTime,
                    'endTime':endTime,
                    'playDays': '5',
                    'routeName':'我的行程',
                };
                //景区
                let scenicCodes = this.props.selectCodes.scenicCodes;
                let scenicCodesStr = '';
                for(let i=0;i<scenicCodes.length;i++){
                    scenicCodesStr = scenicCodesStr + scenicCodes[i] + ',';
                }
                scenicCodesStr = scenicCodesStr.substring(0, scenicCodesStr.lastIndexOf(','));  
                param['sid'] = scenicCodesStr;
                //娱乐
                let funCodes = this.props.selectCodes.funCodes;
                let funCodesStr = '';
                for(let i=0;i<funCodes.length;i++){
                    funCodesStr = funCodesStr + funCodes[i] + ',';
                }
                funCodesStr = funCodesStr.substring(0, funCodesStr.lastIndexOf(','));  
                param['funId'] = funCodesStr;
                //购物
                let shopCodes = this.props.selectCodes.shopCodes;
                let shopCodesStr = '';
                for(let i=0;i<shopCodes.length;i++){
                    shopCodesStr = shopCodesStr + shopCodes[i] + ',';
                }
                shopCodesStr = shopCodesStr.substring(0, shopCodesStr.lastIndexOf(','));  
                param['shoppingId'] = shopCodesStr;
                //酒店
                let hotelCodes = this.props.selectCodes.hotelCodes;
                let hotelCodesStr = '';
                for(let i=0;i<shopCodes.length;i++){
                    hotelCodesStr = hotelCodesStr + hotelCodes[i] + ',';
                }
                hotelCodesStr = hotelCodesStr.substring(0, hotelCodesStr.lastIndexOf(','));  
                param['hotelId'] = hotelCodesStr;
                //餐厅
                let foodCodes = this.props.selectCodes.foodCodes;
                let foodCodesStr = '';
                for(let i=0;i<foodCodes.length;i++){
                    foodCodesStr = foodCodesStr + foodCodes[i] + ',';
                }
                foodCodesStr = foodCodesStr.substring(0, foodCodesStr.lastIndexOf(','));  
                param['restaurantId'] = foodCodesStr;

                this.props.dispatch(asyncSelfCreateTourAction(param)).then(()=>{
                    if(this.props.createTour.status && this.props.createTour.status === '0'){
                        this.props.history.push('/smart-tour/tourDetail');
                    }else{
                        Toast.fail('行程创建失败', 1);                    
                    }
                });
            }else{
                this.props.history.push('/me/login');                
            }
        } else {
            Toast.info('请选择您想要游玩的兴趣点');
        }
        // this.props.history.push('/smart-tour/preference-setting');
    }
}

const mapStateToProps = function (state) {
    return {
        userInfo: SysUtil.getUserInfo(state),
        isUserLogined: SysUtil.isUserLogined(state),
        selectedTabIndex: state.smartTour.elementSelectScreen.selectedTabIndex,
        selectCodes: state.smartTour.elementSelectScreen.selectCodes,
        createTour: state.createTour.listScreen.groups,        
    };
};

ElementSelectScreen = connect(mapStateToProps)(ElementSelectScreen);

export default ElementSelectScreen;


