import React, { Component } from 'react';
import { connect } from 'react-redux';

class TourBudgetPlaneView extends Component
{
    constructor(props)
    {
        super(props);
    }
    componentDidMount()
    {
        
    }
    render()
    {
        let plane_icon=require('../../assets/images/tour/budget_plane_icon.png');
        let plane=require('../../assets/images/tour/budget_plane.png');
        let goto=require('../../assets/images/tour/budget_goto.png');

        return (
            <div className="budgetPlaneViewBox">
                <div className='budgetPlaneViewHead'>
                    <img src={plane_icon} alt='' className='budgetPlaneViewHeadIcon'/>
                    <div className='budgetPlaneViewHeadLine'>
                        <span>北京城区</span>
                        <img src={goto} alt='' />
                        <span>贵阳市</span>
                    </div>
                </div>
                <div className='budgetPlaneViewCard'>
                    <div className='plane'>
                        <span className='cnName'>中国国航</span>
                        <span>CA1493</span>
                    </div>
                    <div className='line'>
                        <div className='startAddress'>
                            <div className='district'>北京城区</div>
                            <div className='address'>北京首都国际机场</div>
                        </div>
                        <div className='planeImg'>
                            <img src={plane} alt=''/>
                        </div>
                        <div className='endAddress'>
                            <div className='district'>贵阳市</div>
                            <div className='address'>贵阳龙洞堡机场</div>
                        </div>
                    </div>
                    <div className='time'>
                        <div className='timeItem'>
                            <div className='key'>航班日期</div>
                            <div className='value'>2017-08-02</div>
                        </div>
                        <div className='timeItem' style={{textAlign:'center'}}>
                            <div className='key'>起飞时间</div>
                            <div className='value'>19:15</div>
                        </div>
                        <div className='timeItem' style={{textAlign:'right'}}>
                            <div className='key'>到达时间</div>
                            <div className='value'>22:10</div>
                        </div>
                    </div>
                </div>
                <div className='budgetPlaneViewSumPrice'>总价:&yen;3122</div>
            </div>
        );
    }
}

const mapStateToProps = function(state, ownProps)
{
    return {
    
    };
};

TourBudgetPlaneView = connect(mapStateToProps, null, null, {withRef: true})(TourBudgetPlaneView);

export default TourBudgetPlaneView;


