import { combineReducers } from 'redux';
import collectActivityReducer from '../../reducers/me/collectActivityReducer';
import collectInformationReducer from '../../reducers/me/collectInformationReducer';
import collectTravelNoteReducer from '../../reducers/me/collectTravelNoteReducer';
import {Actions} from '../../../core/utils/IConstant';

function selectedTabIndexReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.ME.COLLECT.SET_SELECTED_TAB_INDEX:
        return action.index;
    case Actions.ME.COLLECT.GO_BACK:
        return 0;
    default:
        return state;
    }
}

const collectReducer = combineReducers({
    activity: collectActivityReducer,
    information: collectInformationReducer,
    travelNote: collectTravelNoteReducer,
    selectedTabIndex: selectedTabIndexReducer,
});

export default collectReducer;


