import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    let obj = state[action.key];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.PREFERENCR.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [action.key]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.PREFERENCR.LIST.GET_LIST_SUCCESS:
        let dataList = action.result.data;
        for (let i = 0; i < dataList.length; i++) {
            let element = dataList[i];
            let defaultValue = element.defaultValue.toString();
            if (element.options) {
                for (let j = 0; j < element.options.length; j++) {
                    if (defaultValue.indexOf(element.options[j].ZDXLBH) !== -1) {
                        element.options[j].isCheck = true;
                    } else {
                        element.options[j].isCheck = false;
                    }
                }
            }
        }
        return {
            ...state,
            [action.key]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
            }
        };
    case Actions.PREFERENCR.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [action.key]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.PREFERENCR.LIST.GO_BACK:
        return {};
    // case Actions.PREFERENCR_CHANGE_CHECK_STATUS:
    //     return {};
    default:
        return state;
    }
}

const listReducer = combineReducers({
    groups: groupReducer,
});

export default listReducer;


