import { combineReducers } from 'redux';
import { Actions, SpecialStoreKey } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    // 我的收藏活动列表
    if(!action.extraParams || action.extraParams.storeKey !== SpecialStoreKey.COLLECT_ACTIVITY)
    {
        return {...state};
    }
    let obj = state[action.key];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.ACTIVITY.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [action.key]: {
                ...obj,
                isFetching: true,
                pageIndex: action.pageIndex,
            }
        };
    case Actions.ACTIVITY.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        dataList = dataList.concat(action.result.data);
        return {
            ...state,
            [action.key]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
                total: action.result.total,
                pageIndex: action.pageIndex,
                hasMore: (action.result.total > dataList.length)
            }
        };
    case Actions.ACTIVITY.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [action.key]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.ME.COLLECT.ACTIVITY_GO_BACK:
        return {};
    case Actions.ACTIVITY.LIST.DELETE:
        return {
            ...state,
            [action.key]: {
                isFetching: false,
                dataList: [],
            }
        };
    case Actions.ACTIVITY.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [action.key]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    default:
        return state;
    }
}

const collectActivityReducer = combineReducers({
    groups: groupReducer,
});

export default collectActivityReducer;


