import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    // 可能是推荐线路列表、我的线路列表
    // if(action.extraParams && action.extraParams.storeKey && action.extraParams.storeKey.length > 0)
    // {
    //     return {...state};
    // }
    switch(action.type)
    {
    case Actions.SMARTTOUR.LINELIST.GET_LINELIST_REQUEST:
        return {
            ...state, 
            isFetching: true,
            pageIndex: action.pageIndex,
        };
    case Actions.SMARTTOUR.LINELIST.GET_LINELIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : state.dataList;
        dataList = dataList ? dataList : [];
        dataList = dataList.concat(action.result.data);
        return {
            ...state,
            isFetching: false,
            dataList: dataList,
            total: action.result.total,
            pageIndex: action.pageIndex,
            hasMore: (action.result.total > dataList.length)
        };
    case Actions.SMARTTOUR.LINELIST.GET_LINELIST_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.SMARTTOUR.LINELIST.GO_BACK:
        return {};
    case Actions.SMARTTOUR.LINELIST.DELETE:
        return {
            ...state,
            isFetching: false,
            dataList: [],
        };
    case Actions.SMARTTOUR.LINELIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            listViewOffset: action.listViewOffset
        };
    default:
        return state;
    }
}

const listReducer = combineReducers({
    groups: groupReducer,
});

export default listReducer;


