import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function goBackAction(code)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GO_BACK,
        code,
    };
}

// 酒店详情
export function asyncGetDetailAction(code)
{
    return function(dispatch, getState)
    {
        dispatch(_getDetailRequestAction(code));
        return _fetchDetail(code).then((resp)=>{
            resp = (resp && resp.results && resp.results.length > 0) ? resp.results[0] : undefined;
            dispatch(_getDetailSuccessAction(code, resp));
        }).catch((error)=>{
            dispatch(_getDetailFailureAction(code, error));
        });
    };
}

// 酒店详情页面滚动条位置
export function setDetailScrollerPositionAction(code, offset)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.SET_DETAIL_SCROLL_OFFSET,
        code,
        scrollViewOffset: offset,
    };
}

function _fetchDetail(code)
{
    let url = 'http://103.10.86.241:8091/api/Hotel/GetHoteDetail';
    let kvDict = {type: 'app', Code: code};
    return HttpClient.getData(url, kvDict)
        .then((resp) => resp.json());
}

function _getDetailRequestAction(code)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_DETAIL_REQUEST,
        code,
    };
}

function _getDetailSuccessAction(code, resp)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_DETAIL_SUCCESS,
        code,
        detailItem: resp
    };
}

function _getDetailFailureAction(code, error)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_DETAIL_FAILURE,
        code,
        message: error
    };
}


