import { combineReducers } from 'redux';
import { Actions, SpecialStoreKey} from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    // 我的收藏资讯列表
    if(!action.extraParams || action.extraParams.storeKey !== SpecialStoreKey.COLLECT_INFORMATION)
    {
        return {...state};
    }
    switch(action.type)
    {
    case Actions.INFORMATION.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            isFetching: true,
            pageIndex: action.pageIndex,
        };
    case Actions.INFORMATION.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : state.dataList;
        dataList = dataList ? dataList : [];
        dataList = dataList.concat(action.result.data);
        return {
            ...state,
            isFetching: false,
            dataList: dataList,
            total: action.result.total,
            pageIndex: action.pageIndex,
            hasMore: (action.result.total > dataList.length)
        };
    case Actions.INFORMATION.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.INFORMATION.LIST.GO_BACK:
        return {};
    case Actions.ME.COLLECT.INFORMATION_GO_BACK:
        return {};
    case Actions.INFORMATION.LIST.DELETE:
        return {
            ...state,
            isFetching: false,
            dataList: [],
        };
    case Actions.INFORMATION.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            listViewOffset: action.listViewOffset
        };
    default:
        return state;
    }
}

const collectInformationReducer = combineReducers({
    groups: groupReducer,
});

export default collectInformationReducer;


