import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/map.css';
import { goBackAction } from '../../store/actions/map/listAction';
import MapSearchListView from '../../views/map/MapSearchListView';

class mapSearchListPage extends Component
{
    render()
    {
        let listViewHeight = window.innerHeight - 45;

        return (
            <div className='mapSearchListPage'>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.goBack();
                    }}
                    rightContent={
                        <div style={{ display: 'flex', alignItems: 'center', height: '100%' }}>
                            <div className="mapRightButton">
                                <img className="mapRightButtonImage" src={require('../../assets/images/map/search_map.png')} alt='' onClick={()=>this.goBack()}/>
                            </div>
                        </div>
                    }>
                    便利店
                </NavBar>
                <Content>
                    <MapSearchListView
                        onItemClick={this.onItemClick}
                        height={listViewHeight}
                        ref={(listView) => {
                            if (listView) {
                                this.listView = listView.getWrappedInstance();
                            }
                        }} />
                </Content>
            </div>
        );
    }
    goBack(){
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }
    onItemClick(){
        
    }
}

const mapStateToProps = function(state, ownProps)
{
    return {
    };
};

mapSearchListPage = connect(mapStateToProps)(mapSearchListPage);

export default mapSearchListPage;


