import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncDeleteListAction, setScrollerPositionAction, openEditAction } from '../../store/actions/smart-tour/tourlistAction';
import UIUtil from '../../core/utils/UIUtil';
import SysUtil from '../../core/utils/SysUtil';
import HttpClient from '../../core/utils/HttpClient';
import EmptyView from '../../views/common/EmptyView';

class TourListView extends Component {
    constructor(props) {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        this.editLayout = this.editLayout.bind(this);
        this.closeEdit = this.closeEdit.bind(this);

        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount() {
        // 处理返回
        if (!this.props.dataList) {
            this.loadData();
        }
        if (this.listView && !isNaN(this.props.listViewOffset)) {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentWillReceiveProps(nextProps) {
        if (this.props.dataList !== nextProps.dataList) {
            this.setState({ dataSource: this.state.dataSource.cloneWithRows(nextProps.dataList ? nextProps.dataList : []) });
        }
    }
    render() {
        return (
            <div className="listBox" style={{ height: this.props.height }}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh()}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator
                            toast
                            text="Loading..." />
                        : null
                }
            </div>
        );
    }
    renderListView() {
        if (this.props.dataList && this.props.dataList.length > 0) {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );

            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}
                    />}
                    onEndReached={this.loadNextPage}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50} />
            );
        }
    }
    isEmptyString(str) {
        return !str || str.length === 0;
    }
    onRefresh() {
        this.props.dispatch(asyncDeleteListAction()).then(() => {
            // this.loadData();
            let pageIndex = 1;
            this.props.dispatch(asyncGetListAction(pageIndex));
        });
    }
    loadNextPage() {
        this.loadData();
    }
    renderFooter() {
        if (!this.props.isFetching || this.props.hasMore !== true) {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID) {
        let ImgBg = item.imageUrl;

        return (
            <div className="tourListItem"
                style={{ backgroundImage: `url(${ImgBg})` }}
                onClick={() => {
                    if (this.props.onItemClick) {
                        this.props.onItemClick(item);
                    }
                }}>
                <div className="tourListItemEdit" onClick={() => this.editLayout(item)}>
                    <img className="tourListItemEditIcon" src={require('../../assets/images/tour/tour_edit.png')} alt='' />
                    <div className="tourListItemEditStr" >编辑</div>
                </div>

                <div className="tourListItemContent" >
                    <div className="tourListItemPlayTitle" >{item.title}</div>
                    <div className="tourListItemPlayDatelayout" >
                        <div className="tourListItemContentline" ></div>
                        <div className="tourListItemPlayDate" >{item.playDate + '出发'}</div>
                        <div className="tourListItemContentline" ></div>
                    </div>
                </div>

                <div className="tourListItemBottom">
                    <div className="tourListItemTitle" >查看他们的相关行程</div>
                    <img className="tourListItemArrow" src={require('../../assets/images/tour/left_arrow.png')} alt='' />
                </div>

                {
                    this.props.editOpenList && this.contains(this.props.editOpenList, item.code)
                        ?
                        this.renderEditLayout(item)
                        : null
                }

            </div>
        );
    }
    renderEditLayout(item) {
        return (
            <div className="tourListEditLayout">
                <div className="tourListShareLayout">
                    <img className="tourListShareIcon" src={require('../../assets/images/tour/tour_share.png')} alt='' />
                    <div className="tourListShareStr">分享</div>
                </div>
                <div className="tourListDelLayout">
                    <img className="tourListShareIcon" src={require('../../assets/images/tour/tour_delete.png')} alt='' />
                    <div className="tourListShareStr">删除</div>
                </div>
                <img className="tourListCloseIcon" onClick={() => this.closeEdit(item)} src={require('../../assets/images/tour/tour_close.png')} alt='' />
            </div>);
    }
    contains(arr, obj) {
        var i = arr.length;
        while (i--) {
            if (arr[i] === obj) {
                return true;
            }
        }
        return false;
    }
    editLayout(item) {
        this.props.dispatch(openEditAction(item.code, true));
    }
    closeEdit(item) {
        this.props.dispatch(openEditAction(item.code, false));
    }
    loadData() {
        if (this.props.isFetching) {
            return;
        }
        if (this.props.hasMore && this.props.hasMore === false) {
            return;
        }

        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        this.props.dispatch(asyncGetListAction(pageIndex));
    }

    cacheScrollOffset() {
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(offsetY));
    }
}

TourListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

const mapStateToProps = function (state, ownProps) {
    let obj = state.smartTour.tourlistScreen.groups;
    return {
        ...obj,
    };
};

TourListView = connect(mapStateToProps, null, null, { withRef: true })(TourListView);

export default TourListView;


