function _getListRequestAction(id, pageIndex, key) {
    return {
        type: IConstant.Actions.INFORMATION.COMMENT.GET_COMMENT_REQUEST,
        pageIndex,
        id,
        key
    };
}

function _getListSuccessAction(id, pageIndex, result, key) {
    return {
        type: IConstant.Actions.INFORMATION.COMMENT.GET_COMMENT_SUCCESS,
        pageIndex,
        id,
        result,
        key
    };
}

function _getListFailureAction(id, error, key) {
    return {
        type: IConstant.Actions.INFORMATION.COMMENT.GET_COMMENT_FAILURE,
        message: error,
        id,
        key
    };
}


